import { useTab, useTabList, useTabPanel } from "@react-aria/tabs";
import { useTabListState, TabListState } from "@react-stately/tabs";
import { TabListProps } from "@react-types/tabs";
import React from "react";

import { Check } from "@rollouthq/ui/icons/check";
import { Center, Flex } from "@rollouthq/ui/theme";
import { dataAttr } from "@rollouthq/ui/utils/dom";

type ExtraProps = {
  radio?: boolean;
  completedKeys?: string[];
};

type Tabs<T> = TabListProps<T> & ExtraProps;

export function Tabs<T extends object>(props: Tabs<T>) {
  const { radio, completedKeys, ...rest } = props;
  const state = { ...useTabListState(rest), radio, completedKeys };
  const ref = React.useRef<HTMLDivElement | null>(null);
  const { tabListProps } = useTabList(rest, state, ref);

  return (
    <Flex
      className="rollout-tabs"
      css={{ flexDirection: "column", flex: "auto", minWidth: 0 }}
    >
      <Flex
        {...tabListProps}
        ref={ref}
        className="rollout-tab__list"
        css={{
          borderBottom: "3px solid",
          borderBottomColor: "$strokeMedium",
          gap: "24px",
          minWidth: 0,
        }}
      >
        {[...state.collection].map((item) => (
          <Tab key={item.key} item={item} state={state} />
        ))}
      </Flex>

      <TabPanel key={state.selectedItem?.key} state={state} />
    </Flex>
  );
}

type TabState = TabListState<object> & ExtraProps;
type TabProps = {
  item: Record<string, any>;
  state: TabState;
};

function Tab({ item, state }: TabProps) {
  const { key, rendered } = item;
  const ref = React.useRef<HTMLDivElement | null>(null);
  const { tabProps } = useTab({ key }, state, ref);
  const isSelected = state.selectedKey === key;
  const isDisabled = state.disabledKeys.has(key);
  const isCompleted = state.completedKeys?.includes(key);

  return (
    <Flex
      className="rollout-tabs__tab"
      {...tabProps}
      ref={ref}
      data-selected={dataAttr(isSelected)}
      css={{
        overflowX: "hidden",
        padding: "14px",
        fontWeight: "$medium",
        fontSize: "$sm",
        color: isSelected ? "#5D88EF" : "$grayMedium",
        opacity: isDisabled ? "0.5" : undefined,
        cursor: isDisabled ? "not-allowed" : "pointer",
        alignItems: "center",
        gap: "$2",
        minWidth: 0,
      }}
    >
      {state.radio && (
        <Center
          css={{
            size: "18px",
            border: "1.5px solid",
            borderRadius: "99999px",
            padding: "3px",
            color: "white",
            borderColor: isCompleted ? "#5386F7" : " #DFE1E4",
            background: isCompleted ? "#5386F7" : "",
          }}
        >
          {isCompleted && <Check css={{ size: "100%" }} />}
        </Center>
      )}
      {rendered}
    </Flex>
  );
}

type TabPanelProps = {
  state: TabState;
};

function TabPanel({ state, ...props }: TabPanelProps) {
  const ref = React.useRef<HTMLDivElement | null>(null);
  const { tabPanelProps } = useTabPanel(props, state, ref);
  return (
    <Flex
      css={{
        height: "100%",
      }}
      className="rollout-tabs__tabpanel"
      {...tabPanelProps}
      ref={ref}
    >
      {state.selectedItem?.props.children}
    </Flex>
  );
}
