import { SuggestionKeyDownProps, SuggestionProps } from "@tiptap/suggestion";
import React, {
  useState,
  forwardRef,
  useImperativeHandle,
  useEffect,
} from "react";

import { Variable } from "@rollouthq/common";
import { st } from "@rollouthq/ui/theme";

import { MentionMenuSuggestion } from "./mention-menu-suggestion.component";

export type MentionMenuProps = SuggestionProps & {
  suggestions: Variable[];
};

export type MentionMenuRef = {
  onKeyDown: (props: SuggestionKeyDownProps) => boolean;
};

export const MentionMenu = forwardRef<MentionMenuRef, MentionMenuProps>(
  (props, ref) => {
    const { suggestions } = props;

    const [focusedSuggestion, setFocusedSuggestion] = useState<Variable>(
      suggestions[0]
    );

    useEffect(() => {
      if (suggestions.includes(focusedSuggestion) === false) {
        setFocusedSuggestion(suggestions[0]);
      }
    }, [suggestions]);

    const selectItem = (variable: Variable) => {
      props.command(variable);
    };

    const upHandler = () => {
      setFocusedSuggestion((currentFocusedSuggestion) => {
        const currentFocusedIndex =
          currentFocusedSuggestion != null
            ? suggestions.indexOf(currentFocusedSuggestion)
            : -1;
        const isStartOfList = currentFocusedIndex === 0;
        const newIndex = isStartOfList
          ? suggestions.length - 1
          : currentFocusedIndex - 1;
        return suggestions[newIndex]!;
      });
    };

    const downHandler = () => {
      setFocusedSuggestion((currentFocusedSuggestion) => {
        const currentFocusedIndex =
          currentFocusedSuggestion != null
            ? suggestions.indexOf(currentFocusedSuggestion)
            : -1;
        const isEndOfList = currentFocusedIndex === suggestions.length - 1;
        const newIndex = isEndOfList ? 0 : currentFocusedIndex + 1;
        return suggestions[newIndex]!;
      });
    };

    const enterHandler = () => {
      selectItem(focusedSuggestion);
    };

    useImperativeHandle(ref, () => ({
      onKeyDown: ({ event }) => {
        if (event.key === "ArrowUp") {
          upHandler();
          return true;
        }

        if (event.key === "ArrowDown") {
          downHandler();
          return true;
        }

        if (event.key === "Enter") {
          enterHandler();
          return true;
        }

        return false;
      },
    }));

    const suggestionsByCategory = {
      all: suggestions,
    };

    return (
      <st.div
        css={{
          bgColor: "white",
          color: "$text",
          border: "1px solid",
          borderColor: "$strokeMedium",
          boxShadow: "$lg",
          borderRadius: "$base",
          overflow: "hidden",
        }}
      >
        {Object.entries(suggestionsByCategory).map(
          ([groupName, groupSuggestions]) => {
            if (groupSuggestions.length === 0) {
              return null;
            }

            return (
              <st.div key={groupName}>
                {groupSuggestions.map((suggestion: Variable) => (
                  <MentionMenuSuggestion
                    key={suggestion.id}
                    suggestion={suggestion}
                    onSelect={() => selectItem(suggestion)}
                    focusedSuggestion={focusedSuggestion}
                    setFocusedSuggestion={() =>
                      setFocusedSuggestion(suggestion)
                    }
                  />
                ))}
              </st.div>
            );
          }
        )}
      </st.div>
    );
  }
);
