import React from "react";

import { Blockquote as BlockquoteIcon } from "../icons/blockquote";
import { Bold as BoldIcon } from "../icons/bold";
import { Code as CodeIcon } from "../icons/code";
import { Italic as ItalicIcon } from "../icons/italic";
import { Link as LinkIcon } from "../icons/link";
import { Mention as MentionIcon } from "../icons/mention";
import { Strike as StrikeIcon } from "../icons/strike";
import { Underline as UnderlineIcon } from "../icons/underline";
import { useRichEditorContext } from "../rich-editor";
import { RichEditorToolbarButton } from "./rich-editor-toolbar-button";

function MentionButton() {
  const { editor } = useRichEditorContext();

  return (
    <RichEditorToolbarButton
      label="Insert data"
      showLabel
      icon={MentionIcon}
      onClick={() => editor.commands.insertMentionSymbol()}
    />
  );
}

export const controls = {
  mention: MentionButton,

  bold: () => (
    <RichEditorToolbarButton
      label="Bold"
      icon={BoldIcon}
      shouldRender={(editor) => editor.can().toggleBold?.()}
      onClick={(editor) => editor.chain().focus().toggleBold().run()}
      isActive={(editor) => editor.isFocused && editor.isActive("bold")}
    />
  ),
  italic: () => (
    <RichEditorToolbarButton
      label="Italic"
      icon={ItalicIcon}
      shouldRender={(editor) => editor.can().toggleItalic?.()}
      onClick={(editor) => editor.chain().focus().toggleItalic().run()}
      isActive={(editor) => editor.isFocused && editor.isActive("italic")}
    />
  ),
  underline: () => (
    <RichEditorToolbarButton
      label="Underline"
      icon={UnderlineIcon}
      shouldRender={(editor) => editor.can().toggleUnderline?.()}
      onClick={(editor) => editor.chain().focus().toggleUnderline().run()}
      isActive={(editor) => editor.isFocused && editor.isActive("underline")}
    />
  ),
  strike: () => (
    <RichEditorToolbarButton
      label="Strikethrough"
      icon={StrikeIcon}
      shouldRender={(editor) => editor.can().toggleStrike?.()}
      onClick={(editor) => editor.chain().focus().toggleStrike().run()}
      isActive={(editor) => editor.isFocused && editor.isActive("strike")}
    />
  ),
  link: () => (
    <RichEditorToolbarButton
      label="Link"
      icon={LinkIcon}
      shouldRender={(editor) => editor.can().toggleLink?.({ href: "" })}
      onClick={(editor) => {
        const newHref = window.prompt("Enter URL");
        if (newHref) {
          editor.chain().focus().toggleLink({ href: newHref }).run();
        }
      }}
      isActive={(editor) =>
        editor.isFocused && editor.isActive({ format: "link" })
      }
    />
  ),
  code: () => (
    <RichEditorToolbarButton
      label="Code"
      icon={CodeIcon}
      shouldRender={(editor) => editor.can().toggleCode?.()}
      onClick={(editor) => editor.chain().focus().toggleCode().run()}
      isActive={(editor) => editor.isFocused && editor.isActive("code")}
    />
  ),
  blockquote: () => (
    <RichEditorToolbarButton
      label="Quote"
      icon={BlockquoteIcon}
      shouldRender={(editor) => editor.can().toggleBlockquote?.()}
      onClick={(editor) => editor.chain().focus().toggleBlockquote().run()}
      isActive={(editor) => editor.isFocused && editor.isActive("blockquote")}
    />
  ),
};

export const controlsList = [
  controls.bold,
  controls.italic,
  controls.underline,
  controls.strike,
  controls.link,
  controls.code,
  controls.blockquote,
];
