import React from "react";

import { Variable } from "@rollouthq/common";
import { st } from "@rollouthq/ui/theme";

export type CurlyBracketsMenuSuggestionProps = {
  suggestion: Variable;
  onSelect: () => void;
  focusedSuggestion: Variable;
  setFocusedSuggestion: () => void;
};

export const CurlyBracketsMenuSuggestion = function CurlyBracketsMenuSuggestion(
  props: CurlyBracketsMenuSuggestionProps
) {
  const { suggestion, onSelect, focusedSuggestion, setFocusedSuggestion } =
    props;

  const isFocused = suggestion === focusedSuggestion;

  return (
    <st.button
      key={suggestion.id}
      onClick={() => {
        onSelect();
      }}
      onMouseMove={() => {
        setFocusedSuggestion();
      }}
      css={{
        display: "flex",
        width: "100%",
        alignItems: "center",
        cursor: "default",
        paddingX: "$3",
        paddingY: "$2",
        fontSize: "$1",
        fontWeight: 400,
        zIndex: 999,
        backgroundColor: "white",
        ...(isFocused && {
          backgroundColor: "$surfaceSecondary",
        }),
      }}
    >
      <span>{suggestion.label}</span>
    </st.button>
  );
};
