import * as React from "react";

import { CSS } from "@rollouthq/ui/theme";

import { Grid, GridProps } from "./grid";

type SimpleGridOptions = {
  /**
   * The width at which child elements will break into columns. Pass a number for pixel values or a string for any other valid CSS length.
   */
  minChildWidth?: CSS["minWidth"];
  /**
   * The number of columns
   */
  columns?: CSS["columns"];
  /**
   * The gap between the grid items
   */
  spacing?: CSS["gridGap"];
  /**
   * The column gap between the grid items
   */
  spacingX?: CSS["gridGap"];
  /**
   * The row gap between the grid items
   */
  spacingY?: CSS["gridGap"];
};

export type SimpleGridProps = GridProps & SimpleGridOptions;

export const SimpleGrid = (props: SimpleGridProps) => {
  const { columns, spacingX, spacingY, spacing, minChildWidth, ...rest } =
    props;

  const templateColumns = minChildWidth
    ? widthToColumns(minChildWidth)
    : countToColumns(columns);

  return (
    <Grid
      gap={spacing}
      columnGap={spacingX}
      rowGap={spacingY}
      templateColumns={templateColumns}
      {...rest}
    />
  );
};

const isNumber = (num: any) => typeof num === "number";

function toPx(n: string | number) {
  return isNumber(n) ? `${n}px` : n;
}

function widthToColumns(width: any) {
  return `repeat(auto-fit, minmax(${toPx(width)}, 1fr))`;
}

function countToColumns(count: any) {
  return `repeat(${count}, minmax(0, 1fr))`;
}
