import { useUpdateEffect } from "@react-aria/utils";
import Dropcursor from "@tiptap/extension-dropcursor";
import History from "@tiptap/extension-history";
import Paragraph from "@tiptap/extension-paragraph";
import Text from "@tiptap/extension-text";
import { useEditor, JSONContent } from "@tiptap/react";
import React, { ComponentPropsWithRef } from "react";

import {
  AnyBlockDocument,
  Link,
  Mention,
  FORMATTING_EXTENSIONS,
} from "@rollouthq/common";
import { StyledEditorContent } from "@rollouthq/ui/components/rich-editor/rich-editor";
import { st } from "@rollouthq/ui/theme";

export type RichEditorPreviewProps = ComponentPropsWithRef<typeof st.div> & {
  value: JSONContent;
};

export function RichEditorPreview(props: RichEditorPreviewProps) {
  const { value, css, ...containerProps } = props;

  const editor = useEditor({
    content: value,
    editable: false,
    extensions: [
      AnyBlockDocument,
      Dropcursor,
      History,
      Link,
      Mention,
      Paragraph,
      Text,
      ...FORMATTING_EXTENSIONS,
    ],
  });

  useUpdateEffect(() => {
    editor!.commands.setContent(value);
  }, [JSON.stringify(value)]);

  if (editor == null) {
    return null;
  }

  return (
    <st.div
      css={{
        whiteSpace: "nowrap",
        ...css,
      }}
      {...containerProps}
    >
      <StyledEditorContent editor={editor} />
    </st.div>
  );
}
