import React, { ComponentPropsWithoutRef, useState } from "react";

import { Check } from "@rollouthq/ui/icons/check";
import { External } from "@rollouthq/ui/icons/external";
import { Pencil } from "@rollouthq/ui/icons/pencil";
import { Trash } from "@rollouthq/ui/icons/trash";
import { Flex, st } from "@rollouthq/ui/theme";

export type RichEditorLinkMenuProps = {
  href: string;
  onChange: (newHref: string | null) => void;
};

export function RichEditorLinkMenu(props: RichEditorLinkMenuProps) {
  const [isEditing, setIsEditing] = useState(false);
  const [linkUrl, setLinkUrl] = useState(props.href);

  function handleSave(value: string | null = linkUrl) {
    props.onChange(value);
  }

  const editingInput = (
    <st.input
      autoFocus
      value={linkUrl}
      onChange={(ev) => setLinkUrl(ev.target.value)}
      onKeyPress={(ev) => {
        if (ev.key === "Enter") {
          handleSave();
        }
      }}
      css={{
        fontSize: "$1",
      }}
    />
  );

  const previewLink = (
    <st.a
      href={linkUrl}
      target="_blank"
      rel="noopener noreferrer"
      css={{
        mr: "$3",
        wordBreak: "break-all",
        color: "$primary",
        _hover: { color: "#2D469F" },
      }}
    >
      {linkUrl}
      <External css={{ marginLeft: "$2" }} />
    </st.a>
  );

  return (
    <st.div tabIndex={0} css={{ zIndex: 1 }}>
      <Flex
        css={{
          align: "center",
          padding: "$2",
          bgColor: "white",
          borderWidth: "1px",
          borderRadius: "$base",
          fontSize: "$1",
          boxShadow: "$base",
        }}
      >
        <st.div
          css={{
            flexGrow: 1,
            paddingRight: "$1",
            marginRight: "$1",
            borderRightWidth: "1px",
          }}
        >
          {isEditing ? editingInput : previewLink}
        </st.div>

        <st.div css={{ flexShrink: 0 }}>
          {isEditing ? (
            <MinimalIconButton aria-label="Save" onClick={() => handleSave()}>
              <Check />
            </MinimalIconButton>
          ) : (
            <>
              <MinimalIconButton
                aria-label="Edit link"
                onClick={() => setIsEditing(true)}
              >
                <Pencil />
              </MinimalIconButton>

              <MinimalIconButton
                aria-label="Remove link"
                onClick={() => handleSave(null)}
              >
                <Trash />
              </MinimalIconButton>
            </>
          )}
        </st.div>
      </Flex>
    </st.div>
  );
}

function MinimalIconButton(props: ComponentPropsWithoutRef<typeof st.button>) {
  return (
    <st.button
      css={{
        minWidth: "$6",
        opacity: 0.6,
        "&:hover": { opacity: 1 },
      }}
      {...props}
    />
  );
}
