import { ComponentType } from "react";
import React from "react";

import { Variable } from "@rollouthq/common";
import { AtSign } from "@rollouthq/ui/icons/at-sign";
import { TextInput } from "@rollouthq/ui/icons/text-input";
import { IconProps } from "@rollouthq/ui/theme";

const iconByType: Record<Variable["type"], ComponentType<IconProps>> = {
  boolean: TextInput,
  email: AtSign,
  number: TextInput,
  string: TextInput,
};

export type MentionIconProps = IconProps & {
  variableType: Variable["type"];
};

export function MentionIcon(props: MentionIconProps) {
  const { variableType, ...iconProps } = props;
  const Icon = iconByType[props.variableType];
  return <Icon {...iconProps} />;
}
