import { CollectionChildren } from "@react-types/shared";
import React, { ReactElement, Ref, useMemo } from "react";
import { useComboBox, useFilter, useButton } from "react-aria";
import { useComboBoxState } from "react-stately";

import { Spinner } from "@rollouthq/ui/components/spinner";
import { Apps } from "@rollouthq/ui/icons";
import { Cancel } from "@rollouthq/ui/icons/cancel";
import { ChevronDown } from "@rollouthq/ui/icons/chevron-down";
import { dataAttr } from "@rollouthq/ui/utils/dom";
import { mergeRefs } from "@rollouthq/ui/utils/merge-refs";

import { ListBox } from "./listbox";
import { Popover } from "./popover";
import type { AriaComboBoxProps } from "@react-types/combobox";

const NO_OPTIONS_OPTION = {
  id: "NO_OPTIONS",
  key: "NO_OPTIONS",
  name: "No options found",
};

export type ComboBoxProps<T> = {
  optionKey?: keyof T;
  defaultItems: readonly T[];
  isDisabled?: boolean;
  label?: AriaComboBoxProps<T>["label"];
  menuTrigger?: AriaComboBoxProps<T>["menuTrigger"];
  isRequired?: boolean;
  selectedOption: T | undefined;
  onSelectionChange?: (newSelectedOption: T | undefined) => void;
  inputPlaceholder?: string;
  isLoading?: boolean;
  icon?: ReactElement | undefined | null;
  customRef?: Ref<HTMLInputElement>;
  children: CollectionChildren<T & { _comboboxKey: string }>;
  itemWithButton?: boolean;
};

export function ComboBox<T extends object>(props: ComboBoxProps<T>) {
  const {
    inputPlaceholder,
    isRequired,
    icon,
    customRef,
    defaultItems: defaultItemsProp,
    onSelectionChange: onSelectionChangeProp,
    selectedOption,
    itemWithButton,
  } = props;

  const optionKey = props.optionKey ?? "_comboboxKey";

  const { contains } = useFilter({ sensitivity: "base" });
  const isDisabled = props.isDisabled || props.isLoading;

  const defaultItemsPropWithKeys = useMemo(
    () => defaultItemsProp.map(addComboboxKey),
    [defaultItemsProp]
  );

  const defaultItems = useMemo(
    () =>
      defaultItemsPropWithKeys.length === 0
        ? [addComboboxKey(NO_OPTIONS_OPTION as T)]
        : defaultItemsPropWithKeys,
    [defaultItemsPropWithKeys]
  );

  const selectedOptionIndex =
    selectedOption &&
    // support options with object properties
    defaultItemsProp.indexOf(
      defaultItemsProp.find(
        (i) => JSON.stringify(i) === JSON.stringify(selectedOption)
      ) as any
    );

  const selectedOptionWithKey =
    selectedOptionIndex == null ? null : defaultItems[selectedOptionIndex];

  const onSelectionChange = (newKey: React.Key | undefined) => {
    if (newKey == null) {
      onSelectionChangeProp?.(undefined);
    } else {
      const newSelectedOptionIndex = defaultItems.findIndex(
        (i) => i[optionKey] === newKey.toString()
      );

      const newSelectedOption = defaultItemsProp[newSelectedOptionIndex];
      onSelectionChangeProp?.(newSelectedOption);
    }
  };

  const selectedKey = (selectedOptionWithKey?.[optionKey] ?? null) as
    | null
    | string;

  const state = useComboBoxState({
    ...props,
    defaultItems,
    defaultFilter: contains,
    isDisabled,
    onSelectionChange,
    disabledKeys: ["NO_OPTIONS"],
    selectedKey,
  });

  const buttonRef = React.useRef(null);
  const inputRef = React.useRef<HTMLInputElement | null>(null);
  const mergedInputRef = mergeRefs(customRef, inputRef);
  const listBoxRef = React.useRef(null);
  const popoverRef = React.useRef(null);

  const {
    buttonProps: triggerProps,
    inputProps,
    listBoxProps,
    labelProps,
  } = useComboBox(
    {
      ...props,
      children: props.children as CollectionChildren<T>,
      inputRef,
      buttonRef,
      listBoxRef,
      popoverRef,
      isDisabled,
      onSelectionChange,
    },
    state
  );

  const { buttonProps } = useButton(triggerProps, buttonRef);

  function getIcon() {
    if (icon) {
      return icon;
    }
    if (icon === null) {
      return null;
    }
    return <Apps width="16px" height="16px" />;
  }

  function getInputRightElement() {
    if (props.isLoading) {
      return <Spinner className="rollout-combobox__input__spinner" />;
    }
    if (state.isOpen) {
      return <Cancel css={{ width: "$4" }} />;
    }

    return <ChevronDown css={{ width: "$space$3" }} />;
  }

  const clearProps = {
    onClick() {
      onSelectionChange(undefined);
      state.close();
      state.setFocused(false);
    },
  };

  return (
    <div className="rollout-connect-combobox" style={{ position: "relative" }}>
      {props.label && (
        <div className="rollout-connect-combobox__label" {...labelProps}>
          {props.label} {isRequired && <span>(required)</span>}
        </div>
      )}

      <div
        className="rollout-combobox__input__group"
        data-focused={dataAttr(state.isFocused)}
        data-disabled={dataAttr(isDisabled)}
      >
        <div
          className="rollout-combobox__input__left-element"
          data-icon={dataAttr(!!getIcon())}
        >
          {getIcon()}
        </div>
        <input
          {...inputProps}
          ref={mergedInputRef}
          className="rollout-combobox__input"
          placeholder={inputPlaceholder}
          disabled={isDisabled}
        />

        <button
          type="button"
          className="rollout-combobox__input__right-element"
          {...(state.isOpen ? clearProps : buttonProps)}
          data-open={dataAttr(state.isOpen)}
          ref={buttonRef}
        >
          {getInputRightElement()}
        </button>
      </div>

      {state.isOpen && (
        <Popover
          popoverRef={popoverRef}
          isOpen={state.isOpen}
          onClose={state.close}
        >
          <ListBox
            {...listBoxProps}
            itemWithButton={itemWithButton}
            listBoxRef={listBoxRef}
            state={state}
          />
        </Popover>
      )}
    </div>
  );
}

function addComboboxKey<T>(item: T) {
  const _comboboxKey = randomId();

  return {
    ...item,
    _comboboxKey,
  };
}

function randomId(): string {
  const uint32 = window.crypto.getRandomValues(new Uint32Array(1))[0];
  return uint32.toString(16);
}
