import { Editor, Range } from "@tiptap/core";
import { Node } from "@tiptap/core";
import { ReactNodeViewRenderer, ReactRenderer } from "@tiptap/react";
import Suggestion from "@tiptap/suggestion";
import { PluginKey } from "prosemirror-state";
import tippy, { Instance } from "tippy.js";

import {
  curlyBracketsConfig,
  CurlyBracketsOptions,
  CurlyBracketsStorage,
  CurlyBracketsVariable,
  CURLY_BRACKETS_SYMBOL,
  getCurlyBracketsSuggestions,
} from "@rollouthq/common";

import {
  CurlyBracketsMenu,
  CurlyBracketsMenuProps,
  CurlyBracketsMenuRef,
} from "./curly-brackets-menu.component";
import { CurlyBrackets as CurlyBracketsComponent } from "./curly-brackets.component";

export const CurlyBracketsPluginKey = new PluginKey("curly-brackets");

export const CurlyBrackets = Node.create<
  CurlyBracketsOptions,
  CurlyBracketsStorage
>({
  ...curlyBracketsConfig,

  addNodeView() {
    return ReactNodeViewRenderer(CurlyBracketsComponent);
  },

  addProseMirrorPlugins() {
    const { name, editor } = this;
    return [createMenuPlugin({ name, editor })];
  },
});

function insertCurlyBrackets(opts: {
  editor: Editor;
  range: Range;
  name: string;
  props: CurlyBracketsVariable;
}) {
  const { editor, range, props: variable } = opts;

  return editor
    .chain()
    .focus()
    .insertContentAt(range, [
      {
        type: opts.name,
        attrs: {
          variable,
        },
      },
    ])
    .run();
}

function createMenuPlugin(opts: { name: string; editor: Editor }) {
  const curlyBracketsStorage = opts.editor.storage[
    "curly-brackets"
  ] as CurlyBracketsStorage;

  return Suggestion({
    editor: opts.editor,
    char: CURLY_BRACKETS_SYMBOL,
    pluginKey: CurlyBracketsPluginKey,
    command: ({ editor, range, props }) =>
      insertCurlyBrackets({ editor, range, props, name: opts.name }),
    prefixSpace: false,

    allow: ({ editor, range }) => {
      // https://linear.app/playbook-inc/issue/PB-2561/out-of-range-error-when-sidebar-is-open-enter-and-click-dont-seem-to
      try {
        const $from = editor.state.doc.resolve(range.from);
        const type = editor.schema.nodes[opts.name];
        const allow = !!$from.parent.type.contentMatch.matchType(type);
        return allow;
      } catch {
        return false;
      }
    },
    render: () => {
      let reactRenderer: ReactRenderer<
        CurlyBracketsMenuRef,
        CurlyBracketsMenuProps
      >;
      let popup: Instance;

      return {
        onStart: (props) => {
          const { editor, query } = props;
          const suggestions = getCurlyBracketsSuggestions(editor, query);

          reactRenderer = new ReactRenderer(CurlyBracketsMenu, {
            props: {
              ...props,
              suggestions,
            },
            editor: props.editor,
          });

          popup = tippy("body", {
            getReferenceClientRect: props.clientRect,
            appendTo: () => document.body,
            content: reactRenderer.element,
            showOnCreate: true,
            interactive: true,
            trigger: "manual",
            placement: "bottom-start",
          })[0];

          curlyBracketsStorage.menuContainer = popup.popper;
        },

        onUpdate(props) {
          const { editor, query } = props;
          const suggestions = getCurlyBracketsSuggestions(editor, query);

          reactRenderer.updateProps({
            ...props,
            suggestions,
          });

          popup.setProps({
            getReferenceClientRect: props.clientRect,
          });
        },

        onKeyDown(props) {
          if (props.event.key === "Escape") {
            popup.hide();
            return true;
          }

          return reactRenderer.ref?.onKeyDown(props) ?? false;
        },

        onExit() {
          popup.destroy();
          reactRenderer.destroy();
        },
      };
    },
  });
}
