import React from "react";

import { styled, st } from "@rollouthq/ui/theme";
import { randomColor, isDark } from "@rollouthq/ui/utils/color";
import { useImage } from "@rollouthq/ui/utils/use-image";

export type AvatarProps = { src?: string; alt?: string; name?: string };

const Image = styled("img", {
  width: "1em",
  height: "1em",
  borderRadius: "50%",
});

function initials(name: string) {
  const [firstName, lastName] = name.split(" ");
  return firstName && lastName
    ? `${firstName.charAt(0)}${lastName.charAt(0)}`
    : firstName.charAt(0);
}

/**
 * The avatar name container
 */
const AvatarName = (props: any) => {
  const { name, getInitials, ...rest } = props;

  return (
    <st.div
      role="img"
      aria-label={name}
      css={{ fontSize: "$lg", size: "$lg", padding: "2px" }}
      {...rest}
    >
      {name ? getInitials?.(name) : null}
    </st.div>
  );
};

export const baseStyle = {
  display: "inline-flex",
  alignItems: "center",
  justifyContent: "center",
  textAlign: "center",
  textTransform: "uppercase",
  fontWeight: "medium",
  position: "relative",
  flexShrink: 0,
};

const baseStyleContainer = (props: any) => {
  const { name, theme, size = "20px" } = props;
  const bg = name ? randomColor({ string: name }) : "gray.400";
  const isBgDark = isDark(bg)(theme);

  let color = "white";
  if (!isBgDark) {
    color = "gray.800";
  }

  const borderColor = "white";

  return {
    bg,
    color,
    borderColor,
    verticalAlign: "top",
    size,
  };
};

/**
 * Avatar component that renders an user avatar with
 * support for fallback avatar and name-only avatars
 */
export const Avatar = (props: any) => {
  const {
    src,
    srcSet,
    name,
    showBorder,
    borderRadius = "100%",
    onError,
    getInitials = initials,
    icon = <DefaultIcon />,
    iconLabel = " avatar",
    loading,
    children,
    borderColor,
    ignoreFallback,
    ...rest
  } = props;

  const avatarStyles = {
    borderRadius,
    borderWidth: showBorder ? "2px" : undefined,
    ...baseStyle,
    ...baseStyleContainer(props),
  };

  if (borderColor) {
    avatarStyles.borderColor = borderColor;
  }

  return (
    <st.span {...rest} css={avatarStyles}>
      <AvatarImage
        src={src}
        srcSet={srcSet}
        loading={loading}
        onError={onError}
        getInitials={getInitials}
        name={name}
        borderRadius={borderRadius}
        icon={icon}
        iconLabel={iconLabel}
        ignoreFallback={ignoreFallback}
      />
      {children}
    </st.span>
  );
};

const AvatarImage = (props: any) => {
  const {
    src,
    srcSet,
    onError,
    getInitials,
    name,
    borderRadius,
    loading,
    iconLabel,
    icon = <DefaultIcon />,
    ignoreFallback,
  } = props;

  /**
   * use the image hook to only show the image when it has loaded
   */
  const status = useImage({ src, onError, ignoreFallback });

  const hasLoaded = status === "loaded";

  /**
   * Fallback avatar applies under 2 conditions:
   * - If `src` was passed and the image has not loaded or failed to load
   * - If `src` wasn't passed
   *
   * In this case, we'll show either the name avatar or default avatar
   */
  const showFallback = !src || !hasLoaded;

  if (showFallback) {
    return name ? (
      <AvatarName getInitials={getInitials} name={name} />
    ) : (
      React.cloneElement(icon, {
        role: "img",
        "aria-label": iconLabel,
      })
    );
  }

  /**
   * If `src` was passed and the image has loaded, we'll show it
   */
  return (
    <Image
      src={src}
      srcSet={srcSet}
      alt={name}
      loading={loading}
      css={{
        width: "100%",
        height: "100%",
        objectFit: "cover",
        borderRadius,
      }}
    />
  );
};

/**
 * Fallback avatar react component.
 * This should be a generic svg used to represent an avatar
 */
const DefaultIcon = (props: any) => (
  <st.svg
    viewBox="0 0 128 128"
    color="#91959C"
    width="100%"
    height="100%"
    {...props}
  >
    <path
      fill="currentColor"
      d="M103,102.1388 C93.094,111.92 79.3504,118 64.1638,118 C48.8056,118 34.9294,111.768 25,101.7892 L25,95.2 C25,86.8096 31.981,80 40.6,80 L87.4,80 C96.019,80 103,86.8096 103,95.2 L103,102.1388 Z"
    />
    <path
      fill="currentColor"
      d="M63.9961647,24 C51.2938136,24 41,34.2938136 41,46.9961647 C41,59.7061864 51.2938136,70 63.9961647,70 C76.6985159,70 87,59.7061864 87,46.9961647 C87,34.2938136 76.6985159,24 63.9961647,24"
    />
  </st.svg>
);
