import { useEffect, useRef } from "react";

// Returns a function equivalent to `window.setTimeout`, however
// when the component unmounts, all pending timeouts will be cancelled
// instead of running after unmount and potentially trying to
// access an unmounted instance.
export function useTimeouts() {
  const timeouts = useRef<number[]>([]);

  useEffect(() => {
    return () => {
      timeouts.current.forEach((timeout) => {
        window.clearTimeout(timeout);
      });
    };
  }, []);

  function addTimeout(callback: Function, time: number) {
    const timeout = window.setTimeout(callback, time);
    timeouts.current.push(timeout);

    function clearTimeout() {
      window.clearTimeout(timeout);
    }

    return clearTimeout;
  }

  return addTimeout;
}
