import { useHover } from "@react-aria/interactions";
import React from "react";
import { useState } from "react";

import {
  ArrowDown,
  ArrowRight,
  Folder,
  File as FileIcon,
  EmptyBlock,
} from "@rollouthq/ui/icons";

import { File } from "./file-browser";

type FileItemProps = {
  item: File;
  onToggle: () => void;
  onSelectPath: () => void;
  selectionMode: "file" | "folder";
  hasChildren: boolean;
};

export function FileItem(props: FileItemProps) {
  const { item, onToggle, onSelectPath, selectionMode, hasChildren } = props;
  const { isFolder, depth = 0, name } = item;
  const [toggled, setToggled] = useState(false);
  let { hoverProps, isHovered } = useHover({});

  const shouldShowArrow = Boolean(isFolder) && Boolean(hasChildren);
  const visibility = shouldShowArrow ? "visible" : "hidden";

  const arrowIcon = toggled ? (
    <ArrowDown
      onClick={() => {
        onToggle();
        setToggled(!toggled);
      }}
      fontSize="1.25rem"
      style={{
        paddingLeft: "5px",
        cursor: "pointer",
      }}
      color={isHovered ? "black" : "#D3D5DA"}
      fill={isHovered ? "#D3D5DA" : undefined}
    />
  ) : (
    <ArrowRight
      onClick={() => {
        onToggle();
        setToggled(!toggled);
      }}
      style={{
        paddingLeft: "5px",
        cursor: "pointer",
      }}
      fontSize="1.25rem"
      color={isHovered ? "black" : "#D3D5DA"}
      fill={isHovered ? "#D3D5DA" : undefined}
    />
  );

  const spacer = <EmptyBlock fontSize="1.25rem" />;

  const folderIcon = <Folder fontSize="1rem" />;
  const fileIcon = <FileIcon fontSize="1rem" />;

  const paddingLeft =
    depth === 1 && isFolder ? 0 : isFolder ? (depth - 1) * 20 : depth * 20 + 6;

  const selectFile = selectionMode === "file" && !item.isFolder;
  const selectFolder = selectionMode === "folder" && item.isFolder;

  const cursor = selectFile || selectFolder ? "cursor" : "auto";

  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        gap: "6px",
        paddingLeft: `${paddingLeft}px`,
        cursor,
        width: "100%",
      }}
      onClick={() => !isFolder && onSelectPath()}
      {...hoverProps}
    >
      {shouldShowArrow ? arrowIcon : isFolder ? spacer : null}
      <div
        onClick={onSelectPath}
        style={{
          display: "flex",
          padding: "10px",
          gap: "10px",
          alignItems: "center",
        }}
      >
        {isFolder && folderIcon}
        {!isFolder && fileIcon}

        <div className="rollout-connect-filebrowser__list-item-display">
          {name}
        </div>
      </div>
    </div>
  );
}
