import { useUpdateEffect } from "@react-aria/utils";
import { Editor, EditorEvents, JSONContent } from "@tiptap/core";
import Dropcursor from "@tiptap/extension-dropcursor";
import History from "@tiptap/extension-history";
import Paragraph from "@tiptap/extension-paragraph";
import Text from "@tiptap/extension-text";
import { useEditor, EditorContent } from "@tiptap/react";
import React, {
  CSSProperties,
  forwardRef,
  useEffect,
  useImperativeHandle,
  useMemo,
  useRef,
  useState,
} from "react";

import {
  AnyBlockDocument,
  EnterHandler,
  EscHandler,
  MentionStorage,
  ShiftEnterHandler,
  SingleParagraphDocument,
  Variable,
  FORMATTING_EXTENSIONS,
  unresolveMentions,
  CurlyBracketsStorage,
  RichEditorContent,
} from "@rollouthq/common";
import { st } from "@rollouthq/ui/theme";
import { createContext } from "@rollouthq/ui/utils/context";
import { dataAttr } from "@rollouthq/ui/utils/dom";
import { EventOutside } from "@rollouthq/ui/utils/event-outside";
import { isNotNull } from "@rollouthq/ui/utils/typeguards";
import { useCallbackRef } from "@rollouthq/ui/utils/use-callback-ref";

import { CurlyBrackets } from "./extensions/curly-brackets";
import { Link } from "./extensions/link";
import { Mention } from "./extensions/mention";
import { RichEditorToolbar } from "./toolbar/rich-editor-toolbar";

export type RichEditorRef = {
  container: HTMLDivElement | null;
  focus(): void;
};

export type RichEditorProps = {
  value: JSONContent | null;
  mentionVariables?: readonly Variable[] | false;
  curlyBracketVariables?: readonly Variable[] | false;
  inputVariant?: "outline" | "ghost";
  variant?: "full" | "reduced" | "inline";
  isDisabled?: boolean;
  setValue?: (newValue: RichEditorContent | null) => void;
  placeholder?: string;
  placeholderColor?: string;
  styles?: Record<string, Record<string, any>>;
  onEnter?: (currentValue: RichEditorContent | null) => undefined;
  onShiftEnter?: (currentValue: RichEditorContent | null) => undefined;
  onLeave?: (currentValue: RichEditorContent | null) => void;
  onEscape?: () => void;
  linksEnabled?: boolean;
  formattingEnabled?: boolean;
  ["data-cy"]?: string;
  autoFocus?: boolean;
};

export const RichEditor = forwardRef<RichEditorRef, RichEditorProps>(
  function RichEditor(props, ref) {
    const {
      linksEnabled = true,
      formattingEnabled: formattingEnabledProp = true,
      value,
      placeholder = "",
      placeholderColor,
      isDisabled = false,
      styles: stylesProp,
      mentionVariables = null,
      curlyBracketVariables = null,
      variant,
      onEnter,
      onShiftEnter,
      onEscape,
      onLeave,
      setValue,
      autoFocus,
    } = props;

    const isSingleLine = variant === "inline" || variant === "reduced";
    const formatting = isSingleLine ? false : formattingEnabledProp;
    const Document = isSingleLine ? SingleParagraphDocument : AnyBlockDocument;
    const mentionsEnabled = Boolean(mentionVariables);
    const curlyBracketsEnabled = Boolean(curlyBracketVariables);

    const onUpdateHandler = useCallbackRef((p: { editor: Editor }) => {
      setValue?.(getValue(p.editor));
    });

    const enterHandler = useCallbackRef((p: { editor: Editor }) => {
      const onEnterRet = onEnter?.(getValue(p.editor));
      if (onEnterRet != null) {
        return onEnterRet;
      }
      if (isSingleLine && !p.editor.isDestroyed) {
        p.editor.commands.blur();
        return true;
      }
    });

    const shiftEnterHandler = useCallbackRef((p: { editor: Editor }) => {
      onShiftEnter?.(getValue(p.editor));
      return true;
    });

    const escHandler = useCallbackRef(() => {
      onEscape?.();
      return true;
    });

    const blurHandler = useCallbackRef((p: EditorEvents["blur"]) => {
      const { relatedTarget } = p.event;
      const mention = p.editor.storage.mention as MentionStorage | undefined;
      const curlyBrackets = p.editor.storage["curly-brackets"] as
        | CurlyBracketsStorage
        | undefined;

      const ignoredContainers = [
        container.current!,
        mention?.menuContainer ?? null,
        curlyBrackets?.menuContainer ?? null,
      ].filter(isNotNull);

      if (
        relatedTarget &&
        ignoredContainers.some((c) => c.contains(relatedTarget as HTMLElement))
      ) {
        return;
      }

      const newValue = getValue(p.editor);
      onLeave?.(newValue);
      setIsFocused(false);
    });

    const mention = useMemo(
      () =>
        Mention.configure({
          variables: mentionVariables ? mentionVariables : [],
        }),
      []
    );
    const curlyBrackets = useMemo(
      () =>
        CurlyBrackets.configure({
          variables: curlyBracketVariables ? curlyBracketVariables : [],
        }),
      []
    );

    const extensions = [
      Document,
      Dropcursor,
      History,
      ...(linksEnabled ? [Link] : []),
      ...(mentionVariables ? [mention] : []),
      ...(curlyBracketVariables ? [curlyBrackets] : []),
      Paragraph,
      Text,
      ...(formatting ? FORMATTING_EXTENSIONS : []),
      EnterHandler.configure({ handler: enterHandler }),
      ShiftEnterHandler.configure({ handler: shiftEnterHandler }),
      EscHandler.configure({ handler: escHandler }),
    ];

    const editor = useEditor({
      content: value,
      onUpdate: onUpdateHandler,
      editable: !isDisabled,
      editorProps: {
        attributes: {
          "data-placeholder": placeholder,
        },
      },
      extensions,
      onFocus() {
        setIsFocused(true);
      },
      onBlur: blurHandler,
    });

    useEffect(() => {
      if (autoFocus && editor && !editor.isDestroyed) {
        editor.chain().focus("end").run();
      }
    }, [editor]);

    useUpdateEffect(() => {
      if (!isFocused) {
        editor!.commands.setContent(value);
      }
    }, [value]);

    useUpdateEffect(() => {
      editor!.setEditable(!isDisabled);
    }, [isDisabled]);

    useUpdateEffect(() => {
      if (mentionsEnabled) {
        const mentionStorage = editor!.storage.mention as MentionStorage;
        mentionStorage.setVariables(mentionVariables ? mentionVariables : null);
      }
      if (curlyBracketsEnabled) {
        const curlyBracketsStorage = editor!.storage[
          "curly-brackets"
        ] as CurlyBracketsStorage;
        curlyBracketsStorage.setVariables(
          curlyBracketVariables ? curlyBracketVariables : null
        );
      }
    }, [mentionVariables, curlyBracketVariables]);

    useUpdateEffect(() => {
      editor!.setOptions({
        editorProps: {
          ...editor!.options.editorProps,
          attributes: {
            ...editor!.options.editorProps.attributes,
            "data-placeholder": placeholder,
          },
        },
      });
    }, [placeholder]);

    const container = useRef<HTMLDivElement | null>(null);

    const [isFocused, setIsFocused] = useState(false);

    useImperativeHandle(
      ref,
      () => ({
        container: container.current,
        focus() {
          // Focus and set cursor to the end.
          editor?.commands.focus("end");
        },
      }),
      [editor, container.current]
    );

    if (editor == null) {
      return null;
    }

    const context = {
      editor,
      mentionsEnabled,
      curlyBracketsEnabled,
      linksEnabled,
    };

    function getValue(e: Editor): RichEditorContent | null {
      if (e.isEmpty) {
        return null;
      }
      const content = e.getJSON();
      return unresolveMentions(content) as RichEditorContent;
    }

    function onClickOutside() {
      setIsFocused(false);
    }

    return (
      <RichEditorProvider value={context}>
        <EventOutside onClickOutside={onClickOutside}>
          {(containerProps) => (
            <StyledContainer
              ref={container}
              data-is-empty={dataAttr(editor.isEmpty)}
              data-disabled={dataAttr(isDisabled)}
              data-focus={dataAttr(isFocused)}
              css={stylesProp?.container ?? {}}
              {...containerProps}
              className="rollout-rich-editor__container"
            >
              <st.div
                css={stylesProp?.editor ?? {}}
                style={
                  {
                    "--placeholder-color": "placeholderColor",
                  } as CSSProperties
                }
              >
                <StyledEditorContent
                  css={stylesProp?.editor ?? {}}
                  editor={editor}
                />
              </st.div>
              <RichEditorToolbar />
            </StyledContainer>
          )}
        </EventOutside>
      </RichEditorProvider>
    );
  }
);

const StyledContainer = st("div", {
  position: "relative",
  display: "flex",
  flexDirection: "column",
  justifyContent: "space-between",
  marginTop: "4px",
  marginBottom: "4px",
  alignItems: "stretch",
  maxWidth: "100%",
  minWidth: 0,
  borderRadius: "$base",
  transitionDuration: "250ms",
  transitionProperty: "box-shadow, background-color",
  boxShadow: "0 0 0 1px $colors$strokeMedium",

  "&:hover:not([data-disabled])": {
    boxShadow: "0 0 0 1px $colors$surfaceSecondary",
  },

  "&[data-focus]:not([data-disabled])": {
    boxShadow: "0 0 0 1.5px $colors$primary",
  },

  ".rollout-form-control__input[data-error] &": {
    boxShadow: "0 0 0 1px $colors$error",
  },

  "&[data-disabled]": {
    cursor: "not-allowed",
  },

  "&[data-is-empty] .ProseMirror::before": {
    color: "var(--placeholder-color, $colors$gray)",
    content: "attr(data-placeholder)",
    float: "left",
    height: "0",
    pointerEvents: "none",
  },
});

export const StyledEditorContent = st(EditorContent, {
  fontSize: "$3",
  padding: "$2",

  "& a": {
    all: "unset",
    color: "splash.primary",

    "&[data-has-popover]": {
      bgColor: "splash.primary25",
    },

    "&:hover": {
      textDecoration: "underline",
    },
  },

  "& em": {
    all: "unset",
    fontStyle: "italic",
  },

  "& strong": {
    all: "unset",
    fontWeight: 600,
  },

  "& p": {
    all: "unset",
    marginBottom: 1,
    "&:last-child": {
      marginBottom: 0,
    },
  },

  "& ul": {
    all: "unset",
    paddingLeft: 6,
  },

  "& blockquote": {
    all: "unset",
    borderLeftWidth: "0.250rem",
    paddingLeft: 2,
  },

  "& .ProseMirror": {
    "&:focus": {
      boxShadow: "none",
      outline: "none",
    },
  },

  "& .ProseMirror [contenteditable=false]": {
    whiteSpace: "unset",
  },
});

export type RichEditorContext = {
  editor: Editor;
  mentionsEnabled: boolean;
  curlyBracketsEnabled: boolean;
  linksEnabled: boolean;
};

const [RichEditorProvider, useRichEditorContext] =
  createContext<RichEditorContext>({
    name: "RichEditor",
  });

export { useRichEditorContext };
