import React from "react";
import { useEffect, useState } from "react";
import { useListData } from "react-stately";

import { FileItem } from "@rollouthq/ui/components/file-browser/file-item";
import { File as FileIcon, Folder } from "@rollouthq/ui/icons";
import { ComboBox, ComboBoxItem } from "@rollouthq/ui/index";

const DIRECTORY_DELIMITER = "/";
const ROOT_FOLDER_INDICATOR = "";

export type File = {
  path: string;
  name: string;
  id: string;
  isFolder: boolean;
  depth?: number;
  selectedOption?: File;
  hasChildren: boolean;
};

export type FileBrowserProps = {
  selectionMode: "file" | "folder";
  disabled: boolean;
  dependencies?: string[];
  value: string | undefined;
  setValue: (newVal: string | undefined) => void;
  fetchOptions: (folderPath: string) => Promise<File[]>;
};

export function FileBrowser(props: FileBrowserProps) {
  const {
    fetchOptions,
    dependencies,
    disabled,
    value,
    setValue,
    selectionMode,
  } = props;

  const [originalItems, setOriginalItems] = useState<File[] | null>(null);
  const [items, setItems] = useState<File[] | null>(null);
  const [selectedKeys, setSelectedKeys] = useState<string[]>(() => []);
  const [isLoadingOptions, setIsLoadingOptions] = useState(false);
  const [activeOption, setActiveOption] = useState<File | null>(null);

  let list = useListData<File>({
    initialItems: [],
    getKey: (item) => item.name,
  });

  useEffect(() => {
    async function refetch() {
      setIsLoadingOptions(true);
      const dropDownOptions = await fetchOptions(ROOT_FOLDER_INDICATOR);
      const alphabethicalOrderByPath = (a: File, b: File) =>
        a.path > b.path ? 1 : -1;

      const listOptions = dropDownOptions.sort(alphabethicalOrderByPath);
      listOptions.forEach((option: File) => {
        const parts = option.path.split(DIRECTORY_DELIMITER);
        const depth = parts.length - 1;
        const containingFolder = depth && parts.slice(-2, -1).join();
        const parentKey = containingFolder;

        let hasChildren: boolean = false;
        listOptions.forEach((o) => {
          const another = o;
          if (
            another.path.startsWith(option.path) &&
            another.path != option.path
          ) {
            hasChildren = true;
          }
        });

        if (depth === 1) {
          list.append({ ...option, depth, hasChildren });
        } else {
          list.insertAfter(parentKey, { ...option, depth, hasChildren });
        }
      });
      setIsLoadingOptions(false);
    }
    if (!disabled) {
      refetch();
    }
  }, [JSON.stringify(dependencies), disabled]);

  useEffect(() => {
    setOriginalItems(list.items);
    setSelectedKeys([ROOT_FOLDER_INDICATOR]);
  }, [list.items]);

  useEffect(() => {
    const displayedItems = originalItems?.filter((i) => {
      const { path } = i;
      let pathParts = path.split(DIRECTORY_DELIMITER);
      pathParts.pop();
      const parentFolders = pathParts;
      const isExpanded = parentFolders.every((parentFolder) =>
        new Set([ROOT_FOLDER_INDICATOR]).has(parentFolder)
      );
      return isExpanded;
    });
    setItems(displayedItems ?? []);
  }, [originalItems]);

  useEffect(() => {
    const active = items?.find((i) => i.selectedOption)?.selectedOption;
    if (active != null) {
      setActiveOption({ ...active, selectedOption: active });
    }
  }, [items]);

  const folderIcon = <Folder fontSize="1rem" />;
  const fileIcon = <FileIcon fontSize="1rem" />;
  const icon = selectionMode === "file" ? fileIcon : folderIcon;

  function shouldHideSelectedItem(item: File) {
    const expandedFolders = new Set();

    originalItems?.forEach((i) => {
      const { path: p } = i;
      let pathParts = p.split(DIRECTORY_DELIMITER);
      pathParts.pop();
      const parentFolders = pathParts;
      const isExpanded = parentFolders.every((parentFolder) =>
        new Set(selectedKeys).has(parentFolder)
      );
      if (isExpanded) {
        pathParts.forEach((part) => expandedFolders.add(part));
      }
    });

    let thisItemPathParts = item.path?.split(DIRECTORY_DELIMITER);

    if (thisItemPathParts != null) {
      thisItemPathParts.pop();
      const thisItemParentFolders = thisItemPathParts;
      const shouldHide = !thisItemParentFolders.every((parentFolder) =>
        expandedFolders.has(parentFolder)
      );

      return shouldHide;
    }
    return false;
  }

  function handlePathSelection(option: File) {
    setValue(option.path);
    const newSet = new Set(selectedKeys);
    const displayedItems = originalItems?.flatMap((i) => {
      const { path: p } = i;
      let pathParts = p.split(DIRECTORY_DELIMITER);
      pathParts.pop();
      const parentFolders = pathParts;
      const isExpanded = parentFolders.every((parentFolder) =>
        newSet.has(parentFolder)
      );
      return i.id === option.id
        ? { ...i, selectedOption: option }
        : isExpanded
        ? { ...i, selectedOption: option }
        : [];
    });
    setItems(displayedItems ?? []);
    setSelectedKeys([...newSet]);
  }

  return (
    <ComboBox<any>
      aria-label="File browser"
      defaultItems={items ?? []}
      selectedOption={activeOption}
      isDisabled={isLoadingOptions}
      icon={value != null ? icon : null}
      isLoading={isLoadingOptions}
      inputPlaceholder={`Select ${props.selectionMode}`}
      itemWithButton={true}
      onSelectionChange={(selection) => {
        if (selection == null) {
          setValue(undefined);
        }
      }}
      menuTrigger="focus"
      optionKey="id"
    >
      {(item) => {
        const { name, isFolder, selectedOption, hasChildren } = item as File;
        const expandedFolders = new Set();

        if (shouldHideSelectedItem(item)) {
          return null as any;
        }

        return (
          <ComboBoxItem key={item.id} textValue={item.name}>
            <FileItem
              hasChildren={hasChildren}
              selectionMode={selectionMode}
              item={item}
              onToggle={() => {
                const key = name;
                const newSet = new Set(selectedKeys);
                if (newSet.has(key)) {
                  newSet.delete(key);
                } else {
                  newSet.add(key);
                }
                const displayedItems = originalItems?.flatMap((i) => {
                  const { path: p } = i;
                  let pathParts = p.split(DIRECTORY_DELIMITER);
                  pathParts.pop();
                  const parentFolders = pathParts;
                  const isExpanded = parentFolders.every((parentFolder) =>
                    newSet.has(parentFolder)
                  );
                  if (i.id === selectedOption?.id) {
                    return { ...i, selectedOption };
                  }
                  return isExpanded ? { ...i, selectedOption } : [];
                });

                setItems(displayedItems ?? []);
                setSelectedKeys([...newSet]);
              }}
              onSelectPath={() => {
                const option = originalItems?.find((i) => i.id === item.id);
                if (option != null) {
                  if (selectionMode === "folder" && isFolder) {
                    handlePathSelection(option);
                  }
                  if (selectionMode === "file" && !isFolder) {
                    handlePathSelection(option);
                  }
                }
              }}
            />
          </ComboBoxItem>
        );
      }}
    </ComboBox>
  );
}
