import { Editor, Range } from "@tiptap/core";
import { Node } from "@tiptap/core";
import { ReactNodeViewRenderer, ReactRenderer } from "@tiptap/react";
import Suggestion from "@tiptap/suggestion";
import { PluginKey } from "prosemirror-state";
import tippy, { Instance } from "tippy.js";

import {
  mentionConfig,
  MentionStorage,
  MENTION_SYMBOL,
  Variable,
  getSuggestions,
  MentionOptions,
} from "@rollouthq/common";

import {
  MentionMenu,
  MentionMenuProps,
  MentionMenuRef,
} from "./mention-menu.component";
import { Mention as MentionComponent } from "./mention.component";

export const MentionPluginKey = new PluginKey("mention");

export const Mention = Node.create<MentionOptions, MentionStorage>({
  ...mentionConfig,

  addNodeView() {
    return ReactNodeViewRenderer(MentionComponent);
  },

  addProseMirrorPlugins() {
    const { name, editor } = this;
    return [createMenuPlugin({ name, editor })];
  },
});

function insertMention(opts: {
  editor: Editor;
  range: Range;
  name: string;
  props: Variable;
}) {
  const { editor, range, props: variable } = opts;

  // increase range.to by one when the next node is of type "text"
  // and starts with a space character
  const nodeAfter = editor.view.state.selection.$to.nodeAfter;
  const overrideSpace = nodeAfter?.text?.startsWith(" ");

  if (overrideSpace) {
    range.to += 1;
  }

  return editor
    .chain()
    .focus()
    .insertContentAt(range, [
      {
        type: opts.name,
        attrs: {
          variable,
        },
      },
    ])
    .run();
}

function createMenuPlugin(opts: { name: string; editor: Editor }) {
  const mentionStorage = opts.editor.storage.mention as MentionStorage;

  return Suggestion({
    editor: opts.editor,
    char: MENTION_SYMBOL,
    pluginKey: MentionPluginKey,
    command: ({ editor, range, props }) =>
      insertMention({ editor, range, props, name: opts.name }),
    allow: ({ editor, range }) => {
      // https://linear.app/playbook-inc/issue/PB-2561/out-of-range-error-when-sidebar-is-open-enter-and-click-dont-seem-to
      try {
        const $from = editor.state.doc.resolve(range.from);
        const type = editor.schema.nodes[opts.name];
        const allow = !!$from.parent.type.contentMatch.matchType(type);
        return allow;
      } catch {
        return false;
      }
    },
    prefixSpace: false,
    render: () => {
      let reactRenderer: ReactRenderer<MentionMenuRef, MentionMenuProps>;
      let popup: Instance;

      return {
        onStart: (props) => {
          const { editor, query } = props;
          const suggestions = getSuggestions(editor, query);

          reactRenderer = new ReactRenderer(MentionMenu, {
            props: {
              ...props,
              suggestions,
            },
            editor: props.editor,
          });

          popup = tippy("body", {
            getReferenceClientRect: props.clientRect,
            appendTo: () => document.body,
            content: reactRenderer.element,
            showOnCreate: true,
            interactive: true,
            trigger: "manual",
            placement: "bottom-start",
          })[0];

          mentionStorage.menuContainer = popup.popper;
        },

        onUpdate(props) {
          const { editor, query } = props;
          const suggestions = getSuggestions(editor, query);

          reactRenderer.updateProps({
            ...props,
            suggestions,
          });

          popup.setProps({
            getReferenceClientRect: props.clientRect,
          });
        },

        onKeyDown(props) {
          if (props.event.key === "Escape") {
            popup.hide();
            return true;
          }

          return reactRenderer.ref?.onKeyDown(props) ?? false;
        },

        onExit() {
          popup.destroy();
          reactRenderer.destroy();
        },
      };
    },
  });
}
