import { DependencyList, useEffect, useState } from "react";

export function useAsyncMemo<T>(
  factory: () => Promise<T> | undefined | null,
  deps: DependencyList
): {
  value: T | undefined;
  isLoading: boolean;
};

export function useAsyncMemo<T>(
  factory: () => Promise<T> | undefined | null,
  deps: DependencyList,
  initial: T
): {
  value: T;
  isLoading: boolean;
};

export function useAsyncMemo<T>(
  factory: () => Promise<T> | undefined | null,
  deps: DependencyList,
  initial?: T
) {
  const [isLoading, setIsLoading] = useState(true);

  const [value, setValue] = useState<T | undefined>(initial);

  useEffect(() => {
    let cancel = false;
    const promise = factory();

    if (promise == null) {
      return;
    }

    promise
      .then((newVal) => {
        if (!cancel) {
          setValue(newVal);
        }
      })
      .finally(() => {
        if (!cancel) {
          setIsLoading(false);
        }
      });

    return () => {
      cancel = true;
    };
  }, deps);

  return { value, isLoading };
}
