import * as React from "react";
import { ReactNode } from "react";

import { CSS, st } from "@rollouthq/ui/theme";

export type GridProps = {
  area?: CSS["gridArea"];
  templateAreas?: CSS["gridTemplateAreas"];
  gap?: CSS["gridGap"];
  rowGap?: CSS["gridRowGap"];
  columnGap?: CSS["gridColumnGap"];
  column?: CSS["gridColumn"];
  row?: CSS["gridRow"];
  autoFlow?: CSS["gridAutoFlow"];
  autoRows?: CSS["gridAutoRows"];
  templateRows?: CSS["gridTemplateRows"];
  autoColumns?: CSS["gridAutoColumns"];
  templateColumns?: CSS["gridTemplateColumns"];

  css?: CSS;
  className?: string;
  children: ReactNode;
};

export const Grid = (props: GridProps) => {
  const {
    area,
    templateAreas,
    gap,
    rowGap,
    columnGap,
    column,
    row,
    autoFlow,
    autoRows,
    templateRows,
    autoColumns,
    templateColumns,
    css,
    ...rest
  } = props;

  const styles = {
    display: "grid",
    gridArea: area,
    gridTemplateAreas: templateAreas,
    gridGap: gap,
    gridRowGap: rowGap,
    gridColumnGap: columnGap,
    gridAutoColumns: autoColumns,
    gridColumn: column,
    gridRow: row,
    gridAutoFlow: autoFlow,
    gridAutoRows: autoRows,
    gridTemplateRows: templateRows,
    gridTemplateColumns: templateColumns,
    ...css,
  } as const;

  return <st.div css={styles} {...rest} />;
};
