import React from "react";
import { useHover } from "react-aria";

import { GoogleSignInHover, GoogleSignInNormal } from "@rollouthq/ui/icons";

export function GoogleSignIn() {
  let { hoverProps, isHovered } = useHover({});

  return (
    <div {...hoverProps}>
      {isHovered ? <GoogleSignInHover /> : <GoogleSignInNormal />}
    </div>
  );
}
