import { Editor } from "@tiptap/core";
import { NodeViewWrapper } from "@tiptap/react";
import React from "react";

import { Variable } from "@rollouthq/common";
import { st } from "@rollouthq/ui/theme";

export type CurlyBracketsProps = {
  editor: Editor;
  node: { attrs: { variable: Variable } };
};

export function CurlyBrackets(props: CurlyBracketsProps) {
  const { node } = props;
  const { variable } = node.attrs;

  return (
    <NodeViewWrapper as="span">
      <st.span
        css={{
          fontSize: "$2",
          display: "inline-flex",
          borderRadius: "$base",
          alignItems: "center",
          verticalAlign: "unset",
        }}
      >
        {variable.label}
      </st.span>
    </NodeViewWrapper>
  );
}
