import { Editor } from "@tiptap/core";
import { ReactRenderer } from "@tiptap/react";
import { MarkType } from "prosemirror-model";
import { Plugin, PluginKey } from "prosemirror-state";
import tippy from "tippy.js";

import {
  RichEditorLinkMenu,
  RichEditorLinkMenuProps,
} from "./link-menu.component";

type ClickHandlerOptions = {
  editor: Editor;
  type: MarkType;
};

export function clickHandler(options: ClickHandlerOptions): Plugin {
  return new Plugin({
    key: new PluginKey("handleClickLink"),
    props: {
      handleClick: (view, pos, event) => {
        const { editor } = options;

        const attrs = editor.getAttributes(options.type);
        const link = (event.target as HTMLElement)?.closest("a");

        if (link && attrs.href) {
          const component = new ReactRenderer(RichEditorLinkMenu as any, {
            props: {
              href: attrs.href,
              onChange: (newHref) => {
                popup.hide();
                const chain = editor.chain().focus().extendMarkRange("link");
                if (newHref == null) {
                  chain.unsetLink().run();
                } else {
                  chain.setLink({ href: newHref }).run();
                }
              },
            } as RichEditorLinkMenuProps,
            editor: options.editor,
          });

          const [popup] = tippy("body", {
            getReferenceClientRect: () => link.getBoundingClientRect(),
            appendTo: () => document.body,
            content: component.element,
            showOnCreate: true,
            interactive: true,
            trigger: "manual",
            placement: "bottom-start",
            onHidden() {
              popup.destroy();
              component.destroy();
            },
          });

          return true;
        }
        return false;
      },
    },
  });
}
