import React, { useEffect, useRef } from "react";

export interface EventOutsideRenderProps {
  className?: string;
  style?: React.CSSProperties;
  onMouseDown: React.MouseEventHandler;
  onFocus: React.FocusEventHandler;
  onTouchStart: React.TouchEventHandler;
}

export type EventOutsideRenderType = (
  props: EventOutsideRenderProps
) => React.ReactNode;

export interface EventOutsideProps {
  onClickOutside?: (event: MouseEvent | TouchEvent) => void;
  onFocusOutside?: (event: FocusEvent) => void;
  children?: EventOutsideRenderType | React.ReactNode;
  className?: string;
  style?: React.CSSProperties;
  component?: any;
  render?: EventOutsideRenderType;
}

export function EventOutside(props: EventOutsideProps) {
  const clickCaptured = useRef(false);
  const focusCaptured = useRef(false);

  useEffect(() => {
    document.addEventListener("mousedown", documentClick);
    document.addEventListener("focus", documentFocus, true);
    document.addEventListener("touchstart", documentClick);

    return () => {
      document.removeEventListener("mousedown", documentClick);
      document.removeEventListener("focus", documentFocus, true);
      document.removeEventListener("touchstart", documentClick);
    };
  }, []);

  function documentClick(event: MouseEvent | TouchEvent) {
    if (!clickCaptured.current && props.onClickOutside) {
      props.onClickOutside(event);
    }
    clickCaptured.current = false;
  }

  function innerClick() {
    clickCaptured.current = true;
  }

  function documentFocus(event: FocusEvent) {
    if (!focusCaptured.current && props.onFocusOutside) {
      props.onFocusOutside(event);
    }
    focusCaptured.current = false;
  }

  function innerFocus() {
    focusCaptured.current = true;
  }

  function getProps(): EventOutsideRenderProps {
    return {
      ...(props.className ? { className: props.className } : {}),
      ...(props.style ? { style: props.style } : {}),
      onMouseDown: innerClick,
      onFocus: innerFocus,
      onTouchStart: innerClick,
    };
  }

  function renderComponent() {
    return React.createElement(
      props.component || "span",
      getProps(),
      props.children
    );
  }

  const render = props.render || props.children;

  if (typeof render === "function") {
    return render(getProps());
  }

  return renderComponent();
}
