import { useDateFormatter } from "@react-aria/i18n";
import { CalendarState, RangeCalendarState } from "@react-stately/calendar";
import { AriaButtonProps } from "@react-types/button";
import { CalendarPropsBase } from "@react-types/calendar";
import { DOMProps, StyleProps } from "@react-types/shared";
import { HTMLAttributes, MouseEventHandler, RefObject } from "react";
import React from "react";

import { Button } from "@rollouthq/ui/components/button";
import { ChevronDown } from "@rollouthq/ui/icons/chevron-down";
import { Flex, st } from "@rollouthq/ui/theme";

import { CalendarMonth } from "./calendar-month";

interface CalendarBaseProps<T extends CalendarState | RangeCalendarState>
  extends CalendarPropsBase,
    DOMProps,
    StyleProps {
  state: T;
  visibleMonths?: number;
  calendarProps: HTMLAttributes<HTMLElement>;
  nextButtonProps: AriaButtonProps;
  prevButtonProps: AriaButtonProps;
  calendarRef: RefObject<HTMLDivElement>;
}

export function CalendarBase<T extends CalendarState | RangeCalendarState>(
  props: CalendarBaseProps<T>
) {
  let {
    state,
    calendarProps,
    nextButtonProps: nextButtonPropsProp,
    prevButtonProps: prevButtonPropsProp,
    calendarRef: ref,
    visibleMonths = 1,
  } = props;

  const getNextButtonProps = () => {
    const { onPress, ...otherProps } = nextButtonPropsProp;
    return {
      ...otherProps,
      onClick: onPress as unknown as MouseEventHandler<HTMLButtonElement>,
    };
  };
  const nextButtonProps = getNextButtonProps();
  const getPrevButtonProps = () => {
    const { onPress, ...otherProps } = prevButtonPropsProp;
    return {
      ...otherProps,
      onClick: onPress as unknown as MouseEventHandler<HTMLButtonElement>,
    };
  };
  const prevButtonProps = getPrevButtonProps();

  let currentMonth = state.visibleRange.start;
  let monthDateFormatter = useDateFormatter({
    month: "long",
    year: "numeric",
    era: currentMonth.calendar.identifier !== "gregory" ? "long" : undefined,
    calendar: currentMonth.calendar.identifier,
    timeZone: state.timeZone,
  });

  let titles = [];
  let calendars = [];
  for (let i = 0; i < visibleMonths; i++) {
    titles.push(
      <Flex
        key={i}
        css={{ alignItems: "center", justifyContent: "space-between" }}
      >
        {i === 0 && (
          <Button
            {...prevButtonProps}
            variant="ghost"
            className="spectrum-Calendar-prevMonth"
          >
            <ChevronDown css={{ transform: "rotate(90deg)" }} />
          </Button>
        )}

        <st.span css={{ fontWeight: "$medium" }}>
          {monthDateFormatter.format(
            currentMonth.add({ months: i }).toDate(state.timeZone)
          )}
        </st.span>

        {i === visibleMonths - 1 && (
          <Button {...nextButtonProps} variant="ghost">
            <ChevronDown css={{ transform: "rotate(-90deg)" }} />
          </Button>
        )}
      </Flex>
    );

    const d = currentMonth.add({ months: i });
    calendars.push(
      <CalendarMonth
        {...props}
        key={`${d.year}-${d.month}-${d.day}`}
        state={state}
        startDate={d}
      />
    );
  }

  return (
    <Flex
      {...calendarProps}
      ref={ref}
      css={{
        width: "250px",
        shadow: "$md",
        padding: "$2",
        borderRadius: "$md",
        borderStyle: "solid",
        borderColor: "$strokeMedium",
        flexDirection: "column",
        gap: "$2",
      }}
    >
      <div className="spectrum-Calendar-header">{titles}</div>
      <div className="spectrum-Calendar-months">{calendars}</div>
    </Flex>
  );
}
