import { useTextField, AriaTextFieldOptions } from "@react-aria/textfield";
import React from "react";

import { Flex, st } from "@rollouthq/ui/theme";

type TextAreaProps = AriaTextFieldOptions<"textarea"> & {
  rows?: number;
  required?: boolean;
  description?: string;
  className?: string;
};

export function TextArea(props: TextAreaProps) {
  const { label, rows, description, required, className } = props;
  const ref = React.useRef<HTMLTextAreaElement | null>(null);
  const { labelProps, inputProps, descriptionProps, errorMessageProps } =
    useTextField(props, ref);

  return (
    <Flex
      css={{
        flexDirection: "column",
        position: "relative",
        gap: "$2",
      }}
    >
      <Flex
        css={{
          flexDirection: "column",
          gap: "$1",
        }}
      >
        {label && (
          <st.label
            {...labelProps}
            css={{
              display: "flex",
              fontSize: "$2",
              gap: "$1",
              fontWeight: "$medium",
              textAlign: "left",
              alignItems: "center",
            }}
          >
            {label}{" "}
            {required && (
              <st.span css={{ color: "$primary" }}>(required)</st.span>
            )}
          </st.label>
        )}
        {description && (
          <st.label
            {...descriptionProps}
            css={{
              fontSize: "$2",
              textAlign: "left",
              color: "$textSecondary",
            }}
          >
            {description}
          </st.label>
        )}
      </Flex>

      <st.textarea
        rows={rows}
        css={{
          paddingX: "$2",
          paddingY: "$2-5",
          borderRadius: "$md",
          outline: "none",
          borderWidth: "2px",
          borderColor: "$strokeMedium",
          "&:focus": {
            borderColor: "$interactPrimary",
          },

          borderStyle: "solid",
          fontFamily: "inherit",
        }}
        className={className}
        {...inputProps}
        ref={ref}
      />

      {props.errorMessage && (
        <div {...errorMessageProps} style={{ color: "red", fontSize: 12 }}>
          {props.errorMessage}
        </div>
      )}
    </Flex>
  );
}
