import { useTextField, AriaTextFieldOptions } from "@react-aria/textfield";
import { CSS } from "@stitches/react";
import React, { forwardRef, useRef } from "react";

import { Flex, st } from "@rollouthq/ui/theme";
import { dataAttr } from "@rollouthq/ui/utils/dom";
import { mergeRefs } from "@rollouthq/ui/utils/merge-refs";

export type TextFieldProps = AriaTextFieldOptions<"input"> & {
  onClick?: () => void;
  required?: boolean;
  className?: string;
  css?: CSS;
};

export const TextField = forwardRef<HTMLInputElement, TextFieldProps>(
  (props, parentRef) => {
    const {
      label,
      onClick,
      required,
      description,
      isDisabled,
      className,
      css,
    } = props;

    const inputRef = useRef<HTMLInputElement | null>(null);
    const ref = mergeRefs(parentRef, inputRef);
    const { labelProps, inputProps, descriptionProps, errorMessageProps } =
      useTextField(props, inputRef);

    return (
      <Flex
        css={{
          flexDirection: "column",
          position: "relative",
          gap: "$2",
          width: "100%",
        }}
      >
        {(label || description) && (
          <Flex
            css={{
              flexDirection: "column",
              gap: "$1",
            }}
          >
            {label && (
              <st.label
                {...labelProps}
                css={{
                  display: "flex",
                  fontSize: "$2",
                  gap: "$1",
                  fontWeight: "$medium",
                  textAlign: "left",
                  alignItems: "center",
                }}
              >
                {label}{" "}
                {required && (
                  <st.span css={{ color: "$primary" }}>(required)</st.span>
                )}
              </st.label>
            )}

            {description && (
              <st.label
                {...descriptionProps}
                css={{
                  fontSize: "$2",
                  textAlign: "left",
                  color: "$textSecondary",
                }}
              >
                {description}
              </st.label>
            )}
          </Flex>
        )}

        <st.input
          ref={ref}
          className={className}
          onClick={onClick}
          data-disabled={dataAttr(isDisabled)}
          css={css ? css : undefined}
          {...inputProps}
        />

        {props.errorMessage && (
          <div {...errorMessageProps} style={{ color: "red", fontSize: 12 }}>
            {props.errorMessage}
          </div>
        )}
      </Flex>
    );
  }
);
