import { useCombobox, useMultipleSelection } from "downshift";
import React, { useEffect, useState } from "react";
import { Item } from "react-stately";

import { Spinner } from "@rollouthq/ui/components/spinner";
import { Tabs } from "@rollouthq/ui/components/tabs";
import { ChevronDown } from "@rollouthq/ui/icons";
import { Check } from "@rollouthq/ui/icons/check";
import { Info } from "@rollouthq/ui/icons/info";
import { dataAttr } from "@rollouthq/ui/utils/dom";

import { Popover } from "./popover";

export type MultiSelectOption = {
  id: string;
  name: string;
};

type MultiSelectProps = {
  items: MultiSelectOption[];
  value: string | undefined;
  label?: string;
  isRequired?: boolean;
  isDisabled?: boolean;
  inputPlaceholder?: string;
  isLoading?: boolean;
  onSelectionChange: (items: MultiSelectOption[]) => void;
};

export function MultiSelect(props: MultiSelectProps) {
  const { items: options, isDisabled, inputPlaceholder, value } = props;

  const buttonRef = React.useRef(null);
  const inputRef = React.useRef<HTMLInputElement | null>(null);
  const popoverRef = React.useRef(null);

  const selectedIds = value != null ? value.split(", ") : [];
  const initialSelectedItems = selectedIds.flatMap((id) => {
    const option = options.find((o) => o.id === id);
    return option != null ? option : [];
  });

  const [inputValue, setInputValue] = useState<string | undefined>("");
  const [initialSet, setInitial] = useState<boolean>(false);

  const {
    getSelectedItemProps,
    getDropdownProps,
    addSelectedItem,
    removeSelectedItem,
    setSelectedItems,
    selectedItems,
  } = useMultipleSelection<MultiSelectOption>({
    onSelectedItemsChange: ({ selectedItems: items }) => {
      if (items) {
        props.onSelectionChange(items);
      }
    },
  });

  useEffect(() => {
    if (initialSelectedItems.length > 0 && !initialSet) {
      setInitial(true);
      setSelectedItems(initialSelectedItems);
    }
  }, [initialSelectedItems]);

  const getFilteredItems = (items: MultiSelectOption[]) =>
    items.filter((item) => {
      return (
        inputValue == null ||
        item.name.toLowerCase().startsWith(inputValue.toLowerCase())
      );
    });

  const onSelectedItemChange = () => {
    inputRef?.current?.focus();
  };

  const {
    isOpen,
    getToggleButtonProps,
    getMenuProps,
    getInputProps,
    getComboboxProps,
    highlightedIndex,
    getItemProps,
    reset,
    openMenu,
    closeMenu,
  } = useCombobox({
    inputValue,
    items: getFilteredItems(options),
    onSelectedItemChange,
    onStateChange: ({ inputValue: newInputValue, type, selectedItem }) => {
      switch (type) {
        case useCombobox.stateChangeTypes.InputChange:
          setInputValue(newInputValue);
          break;
        case useCombobox.stateChangeTypes.InputKeyDownEnter:
        case useCombobox.stateChangeTypes.ItemClick:
        case useCombobox.stateChangeTypes.InputBlur:
          if (selectedItem) {
            setInputValue("");
            addSelectedItem(selectedItem);
            reset();
          }
      }
    },
  });

  const filteredList = getFilteredItems(options).map((item, index) => {
    const isSelected = selectedItems.findIndex((t) => t.id === item.id) !== -1;
    const itemProps = getItemProps({ item, index });
    return (
      <li
        className="rollout-select__option"
        data-focused={dataAttr(highlightedIndex === index)}
        data-selected={dataAttr(isSelected)}
        key={`${item.id}${index}`}
        {...itemProps}
        onClick={(e) => {
          if (isSelected) {
            removeSelectedItem(item);
          } else {
            itemProps.onClick(e);
          }
        }}
      >
        <span>{item.name}</span>{" "}
        {isSelected && <Check className="rollout-select__option-check__icon" />}
      </li>
    );
  });

  const selectContent = (
    <div {...getMenuProps()}>
      {isOpen && (
        <Popover popoverRef={popoverRef} isOpen={isOpen} onClose={closeMenu}>
          <div className="rollout-select__content">
            <Tabs aria-label="Trigger options">
              <Item key="select" title="Select option">
                <ul className="rollout-listbox">
                  <div className="rollout-select__multiple-alert">
                    <Info className="rollout-select__multiple-alert__icon" />
                    <span>You can select multiple</span>
                  </div>

                  {filteredList}
                </ul>
              </Item>
            </Tabs>
          </div>
        </Popover>
      )}
    </div>
  );

  return (
    <div className="rollout-connect-select" style={{ position: "relative" }}>
      <div
        className="rollout-select__input__group"
        {...getComboboxProps()}
        data-disabled={dataAttr(isDisabled)}
        onClick={() => inputRef.current?.focus()}
      >
        {selectedItems.map((selectedItem, index) => (
          <span className="rollout-select__tag" key={`selected-item-${index}`}>
            <span {...getSelectedItemProps({ selectedItem, index })}>
              {selectedItem.name}
            </span>
            <span
              onClick={() => removeSelectedItem(selectedItem)}
              className="rollout-select__tag-icon"
            >
              &#10005;
            </span>
          </span>
        ))}

        <input
          {...getInputProps(getDropdownProps({ preventKeyAction: isOpen }))}
          ref={inputRef}
          disabled={isDisabled || props.isLoading}
          className="rollout-select__input"
          placeholder={inputPlaceholder}
          onFocus={() => {
            if (!isDisabled && !props.isLoading) {
              openMenu();
            }
          }}
        />
        <button
          {...getToggleButtonProps()}
          type="button"
          ref={buttonRef}
          aria-label="toggle menu"
          className="rollout-select__input__right-element"
        >
          {props.isLoading ? (
            <Spinner className="rollout-select__input__spinner" />
          ) : (
            <ChevronDown css={{ width: "$space$3" }} />
          )}
        </button>
      </div>

      {selectContent}
    </div>
  );
}
