import { useFocusRing } from "@react-aria/focus";
import { useSwitch } from "@react-aria/switch";
import { useToggleState } from "@react-stately/toggle";
import { AriaSwitchProps } from "@react-types/switch";
import React from "react";

import { st } from "@rollouthq/ui/theme";
import type { CSS } from "@rollouthq/ui/theme";

export function Switch(props: AriaSwitchProps & { css?: CSS }) {
  const { css, ...rest } = props;
  let state = useToggleState(rest);
  let ref = React.useRef<HTMLInputElement | null>(null);
  let { inputProps } = useSwitch(rest, state, ref);
  let { isFocusVisible, focusProps } = useFocusRing();

  return (
    <st.label
      css={{
        "--switch-width": "30px",
        "--slider-size": "16px",
        "--slider-padding": "2px",
        position: "relative",
        display: "inline-block",
        width: "var(--switch-width)",
        height: "20px",
        input: {
          size: 0,
          opacity: 0,
        },
        "input:checked + [data-toggle-slider]": {
          background: "#56BA70",
        },

        "input:focus + [data-toggle-slider]": {
          boxShadow: "0 0 3px #2196F3",
        },

        "input:checked + [data-toggle-slider]:before": {
          transform:
            "translate(calc(var(--switch-width) - ( var(--slider-size) + ( 2 * var(--slider-padding) ) ) ), -50%)",
        },
        ...css,
      }}
    >
      <input {...inputProps} {...focusProps} ref={ref} />
      <st.span
        data-toggle-slider
        css={{
          position: "absolute",
          cursor: "pointer",
          inset: 0,
          background: "#ccc",
          transition: ".4s",
          borderRadius: "20px",
          "&:before": {
            borderRadius: "50%",
            position: "absolute",
            content: "''",
            size: "var(--slider-size)",
            top: "50%",
            transform: "translateY(-50%)",
            left: "var(--slider-padding)",
            background: "white",
            transition: ".4s",
          },
        }}
      ></st.span>
    </st.label>
  );
}
