import { Editor } from "@tiptap/core";
import { NodeViewWrapper } from "@tiptap/react";
import React from "react";

import { Variable } from "@rollouthq/common";
import { st } from "@rollouthq/ui/theme";

import { MentionIcon } from "./mention-icon.component";

export type MentionProps = {
  editor: Editor;
  node: { attrs: { variable: Variable } };
};

export function Mention(props: MentionProps) {
  const { node } = props;
  const { variable } = node.attrs;
  if (variable == null) {
    return null;
  }

  return (
    <NodeViewWrapper as="span">
      <st.span
        css={{
          display: "inline-flex",
          borderRadius: "$base",
          paddingX: "$1",
          alignItems: "center",
          verticalAlign: "middle",
          backgroundColor: "$surfaceSecondary",
          border: "1px solid",
          borderColor: "$strokeMedium",
        }}
      >
        <MentionIcon
          variableType={variable.type}
          css={{ width: "$space$3", marginRight: "$1", color: "$grayMedium" }}
        />
        {variable.label}
      </st.span>
    </NodeViewWrapper>
  );
}
