import React from "react";

import { Variable } from "@rollouthq/common";
import { st } from "@rollouthq/ui/theme";

import { MentionIcon } from "./mention-icon.component";

export type MentionMenuSuggestionProps = {
  suggestion: Variable;
  onSelect: () => void;
  focusedSuggestion: Variable;
  setFocusedSuggestion: () => void;
};

export const MentionMenuSuggestion = function MentionMenuSuggestion(
  props: MentionMenuSuggestionProps
) {
  const { suggestion, onSelect, focusedSuggestion, setFocusedSuggestion } =
    props;

  const isFocused = suggestion === focusedSuggestion;

  return (
    <st.button
      key={suggestion.id}
      onClick={() => {
        onSelect();
      }}
      onMouseMove={() => {
        setFocusedSuggestion();
      }}
      css={{
        display: "flex",
        width: "100%",
        alignItems: "center",
        cursor: "default",
        paddingX: "$3",
        paddingY: "$2",
        fontSize: "$2",
        fontWeight: 500,
        zIndex: 999,
        backgroundColor: "white",
        ...(isFocused && {
          backgroundColor: "$surfaceSecondary",
        }),
      }}
    >
      <MentionIcon
        variableType={suggestion.type}
        css={{ width: "$space$4", marginRight: "$2", color: "$grayMedium" }}
      />
      <span>{suggestion.label}</span>
    </st.button>
  );
};
