import React, { CSSProperties, ReactElement } from "react";
import { useComboBox, useFilter, useButton } from "react-aria";
import { Item, useComboBoxState } from "react-stately";

import { ListBox } from "@rollouthq/ui/components/combobox/listbox";
import { Spinner } from "@rollouthq/ui/components/spinner";
import { Tabs } from "@rollouthq/ui/components/tabs";
import { ChevronDown } from "@rollouthq/ui/icons";
import { st } from "@rollouthq/ui/theme";
import { dataAttr } from "@rollouthq/ui/utils/dom";

import { Popover } from "./popover";
import type { AriaComboBoxProps } from "@react-types/combobox";

export type SelectProps<T> = AriaComboBoxProps<T> & {
  inputPlaceholder?: string;
  isLoading?: boolean;
  icon?: ReactElement | undefined | null;
  onChange?: AriaComboBoxProps<T>["onSelectionChange"];
  styles?: CSSProperties;
};

export function Select<T extends Record<string, any>>(
  uncProps: SelectProps<T>
) {
  type SelectType = SelectProps<T>;
  const {
    defaultSelectedKey,
    items: options,
    menuTrigger: menuTriggerProp = "focus",
    styles,
    ...rest
  } = uncProps;

  const optionList = options as T[];

  // Store ComboBox input value, selected option, open state, and items
  // in a state tracker
  const [fieldState, setFieldState] = React.useState({
    selectedKey: [defaultSelectedKey || ""],
    items: optionList,
  });

  const { startsWith } = useFilter({ sensitivity: "base" });

  const onSelectionChange: SelectType["onSelectionChange"] = (key) => {
    setFieldState((prevState) => {
      let selectedItem = prevState.items?.find((option) => option.id === key);
      return {
        ...prevState,
        selectedKey: [key as string],
      };
    });
    props?.onChange?.(key);
  };

  // Show entire list if user opens the menu manually
  const onOpenChange: SelectType["onOpenChange"] = () => {
    setFieldState((prevState) => ({
      ...prevState,
      items: optionList,
    }));
  };

  const clearSelect = () => {
    setFieldState((prevState) => {
      return {
        ...prevState,
        selectedKey: [],
      };
    });
  };

  const props = {
    items: fieldState.items,
    selectedKey: fieldState.selectedKey[0],
    menuTrigger: menuTriggerProp,
    onOpenChange,
    onSelectionChange,
    ...rest,
  };

  const { inputPlaceholder, isRequired, icon } = props;
  const { contains } = useFilter({ sensitivity: "base" });
  const state = useComboBoxState({
    ...props,
    defaultFilter: contains,
  });

  const buttonRef = React.useRef(null);
  const inputRef = React.useRef<HTMLInputElement | null>(null);
  const listBoxRef = React.useRef(null);
  const popoverRef = React.useRef(null);

  const {
    buttonProps: triggerProps,
    inputProps,
    listBoxProps,
    labelProps,
  } = useComboBox(
    {
      ...props,
      inputRef,
      buttonRef,
      listBoxRef,
      popoverRef,
    },
    state
  );

  const { buttonProps } = useButton(triggerProps, buttonRef);

  const selected = optionList?.find(
    (option) => option.id === fieldState.selectedKey[0]
  );

  return (
    <div
      aria-label="Select input"
      className="rollout-connect-select"
      style={{ position: "relative", ...styles }}
    >
      {props.label && (
        <div className="rollout-connect-select__label" {...labelProps}>
          {props.label} {isRequired && <span>(required)</span>}
        </div>
      )}

      <div
        className="rollout-select__input__group"
        data-focused={dataAttr(state.isFocused)}
        data-disabled={dataAttr(props.isDisabled)}
        onClick={() => inputRef.current?.focus()}
      >
        {selected && (
          <div className="rollout-select__tags">
            <st.div className="rollout-select__tag">
              <span>{selected?.name}</span>
              <st.span
                className="rollout-select__tag-icon"
                onClick={clearSelect}
              >
                x
              </st.span>
            </st.div>
          </div>
        )}
        <input
          {...inputProps}
          ref={inputRef}
          className="rollout-select__input"
          placeholder={inputPlaceholder}
        />
        <button
          type="button"
          className="rollout-select__input__right-element"
          {...buttonProps}
          ref={buttonRef}
        >
          {props.isLoading ? (
            <Spinner className="rollout-select__input__spinner" />
          ) : (
            <ChevronDown css={{ width: "$space$3" }} />
          )}
        </button>
      </div>

      {state.isOpen && (
        <Popover
          popoverRef={popoverRef}
          isOpen={state.isOpen}
          onClose={state.close}
        >
          <div className="rollout-select__content">
            <Tabs aria-label="Trigger options">
              <Item aria-label="Select option" key="select" title="Select">
                <ListBox
                  {...listBoxProps}
                  listBoxRef={listBoxRef}
                  state={state}
                />
              </Item>
            </Tabs>
          </div>
        </Popover>
      )}
    </div>
  );
}
