import { Editor } from "@tiptap/core";
import React, {
  ComponentPropsWithoutRef,
  ComponentType,
  forwardRef,
} from "react";

import {
  Tooltip,
  TooltipArrow,
  TooltipContent,
  TooltipTrigger,
} from "@rollouthq/ui/components/tooltip/tooltip";
import { IconProps, st } from "@rollouthq/ui/theme";
import { dataAttr } from "@rollouthq/ui/utils/dom";

import { useRichEditorContext } from "../rich-editor";

export type RichEditorToolbarButtonProps = Omit<
  ComponentPropsWithoutRef<typeof st.button>,
  "onClick"
> & {
  shouldRender?: (editor: Editor) => boolean;
  label: string;
  showLabel?: boolean;
  icon: ComponentType<IconProps>;
  isActive?: (editor: Editor) => boolean;
  onClick?: (editor: Editor) => void;
};

export const RichEditorToolbarButton = forwardRef<
  HTMLButtonElement,
  RichEditorToolbarButtonProps
>(function RichEditorToolbarButton(props, ref) {
  const {
    label,
    showLabel = false,
    icon: Icon,
    onClick,
    isActive: isActiveFn,
    shouldRender: shouldRenderFn,
    ...restProps
  } = props;
  const { editor } = useRichEditorContext();

  const isActive = isActiveFn?.(editor) ?? false;
  const shouldRender =
    shouldRenderFn == null ? true : shouldRenderFn(editor) ?? false;

  if (!shouldRender) {
    return null;
  }

  return (
    <Tooltip>
      <TooltipTrigger asChild>
        <StyledButton
          ref={ref}
          type="button"
          data-selected={dataAttr(isActive)}
          aria-label={label}
          onClick={() => onClick?.(editor)}
          tabIndex={-1}
          {...restProps}
        >
          <Icon css={{ display: "block" }} />
          {showLabel && label}
        </StyledButton>
      </TooltipTrigger>

      <TooltipContent sideOffset={5}>
        {label}
        <TooltipArrow />
      </TooltipContent>
    </Tooltip>
  );
});

const StyledButton = st("button", {
  all: "unset",
  display: "flex",
  alignItems: "center",
  padding: "$1",
  border: "1px solid",
  borderColor: "$strokeMedium",
  borderRadius: "4px",
  fontSize: "$2",
  gap: "$1",
  color: "$neutral-2",
  "&:hover": {
    color: "$text",
  },
  "&[data-selected]": {
    color: "$primary",
  },
});
