import { useFocusRing } from "@react-aria/focus";
import { useRadio, useRadioGroup } from "@react-aria/radio";
import { VisuallyHidden } from "@react-aria/visually-hidden";
import { RadioGroupState, useRadioGroupState } from "@react-stately/radio";
import { RadioGroupProps, RadioProps } from "@react-types/radio";
import React, {
  createContext,
  PropsWithChildren,
  useContext,
  useRef,
} from "react";

let RadioContext = createContext({} as RadioGroupState);

export function RadioGroup(props: PropsWithChildren<RadioGroupProps>) {
  const { children, label } = props;
  const state = useRadioGroupState(props);
  const { radioGroupProps, labelProps } = useRadioGroup(props, state);

  return (
    <div {...radioGroupProps}>
      <span {...labelProps}>{label}</span>
      <RadioContext.Provider value={state}>{children}</RadioContext.Provider>
    </div>
  );
}

type RadioProp = PropsWithChildren<RadioProps> & {
  activeColor: string;
  onClick: () => void;
};

export function Radio(props: RadioProp) {
  const { children, activeColor, onClick } = props;
  const state = useContext(RadioContext);
  const ref = useRef(null);
  const { inputProps } = useRadio(props, state, ref);
  const { isFocusVisible, focusProps } = useFocusRing();

  const isSelected = state.selectedValue === props.value;
  const strokeWidth = isSelected ? 6 : 2;

  return (
    <label
      style={{ display: "flex", alignItems: "center", cursor: "pointer" }}
      onClick={onClick}
    >
      <VisuallyHidden>
        <input {...inputProps} {...focusProps} ref={ref} />
      </VisuallyHidden>
      <svg width={24} height={24} aria-hidden="true" style={{ marginRight: 4 }}>
        <circle
          cx={12}
          cy={12}
          r={8 - strokeWidth / 2}
          fill="none"
          stroke={isSelected ? activeColor : "gray"}
          strokeWidth={strokeWidth}
        />
        {isFocusVisible && (
          <circle
            cx={12}
            cy={12}
            r={11}
            fill="none"
            stroke="orange"
            strokeWidth={2}
          />
        )}
      </svg>
      {children}
    </label>
  );
}
