import React, { forwardRef } from "react";
import { ComponentPropsWithoutRef } from "react";

import { styled } from "@rollouthq/ui/theme";

type UnstyledButtonProps = ComponentPropsWithoutRef<"button"> & {
  isDisabled?: boolean;
};

const UnstyledButton = forwardRef<HTMLButtonElement, UnstyledButtonProps>(
  (props, ref) => {
    const { isDisabled, ...buttonProps } = props;
    return (
      <button type="button" ref={ref} disabled={isDisabled} {...buttonProps} />
    );
  }
);

export const Button = styled(UnstyledButton, {
  $$scale: "var(--scale, 1)",

  all: "unset",
  display: "inline-flex",
  alignItems: "center",
  gap: "4px",
  fontSize: "calc(1rem * $$scale * 0.8125)",
  paddingX: "calc(1rem * $$scale * 0.5)",
  paddingY: "calc(1rem * $$scale * 0.5)",
  borderRadius: "calc(1rem * $$scale * 0.5)",
  cursor: "pointer",
  userSelect: "none",
  fontWeight: 500,
  transitionDuration: "250ms",
  transitionProperty: "background-color, color",

  "&[disabled]": {
    cursor: "default",
    opacity: 0.3,
  },

  variants: {
    variant: {
      primary: {
        color: "white",
        backgroundColor: "$primary-1",
        "&:hover:not([disabled])": {
          backgroundColor: "$primaryDark",
        },
      },

      outline: {
        color: "$primary-1",
        borderWidth: "1px",
        borderStyle: "solid",
        borderColor: "$primary-1",
        "&:hover:not([disabled])": {
          backgroundColor: "$primaryDark",
          color: "white",
        },
      },

      ghost: {
        backgroundColor: "white",
        "&:hover:not([disabled])": {
          backgroundColor: "$gray",
        },
      },
    },
  },

  defaultVariants: {
    variant: "primary",
  },
});
