import ResizeObserver from "rc-resize-observer";
import React, { forwardRef, ReactElement, useState } from "react";

import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@rollouthq/ui/components/popover";
import { ThreeDots } from "@rollouthq/ui/icons/three-dots";
import { Flex, st } from "@rollouthq/ui/theme";

import { useRichEditorContext } from "../rich-editor";
import { controls, controlsList } from "./controls";
import { RichEditorToolbarButton } from "./rich-editor-toolbar-button";

const layoutMaxControls = {
  sm: 6,
  md: 12,
  lg: 20,
};

export function RichEditorToolbar() {
  const [layout, setLayout] = useState<keyof typeof layoutMaxControls>("lg");
  const maxControls = layoutMaxControls[layout];

  const { mentionsEnabled, linksEnabled } = useRichEditorContext();

  function makeFullLayout(maxButtons: number) {
    const enabledControls = controlsList.filter(
      (ctl) => ctl !== controls.link || linksEnabled
    );
    const hasOverflow = enabledControls.length > maxButtons;

    return (
      <Flex css={{ justifyContent: "space-between", paddingX: "$1" }}>
        <Flex>
          {enabledControls.slice(0, maxButtons).map((Control, controlIndex) => (
            <Control key={controlIndex} />
          ))}
        </Flex>

        <Flex>
          {mentionsEnabled && <controls.mention />}

          {hasOverflow && (
            <MoreToolsButton>
              {enabledControls
                .slice(maxButtons)
                .map((Control, controlIndex) => (
                  <Control key={controlIndex} />
                ))}
            </MoreToolsButton>
          )}
        </Flex>
      </Flex>
    );
  }

  const renderedControls = makeFullLayout(maxControls);

  function handleResize() {}

  return (
    <ResizeObserver onResize={handleResize}>
      <StyledToolbar>
        <st.div css={{ overflow: "hidden" }}>{renderedControls}</st.div>
      </StyledToolbar>
    </ResizeObserver>
  );
}

const StyledToolbar = st("div", {
  display: "none",
  ".rollout-rich-editor__container[data-focus] &": {
    display: "block",
  },
  width: "100%",
  paddingBottom: "$1",
  fontSize: "$3",
});

type MoreToolsButtonProps = {
  children: ReactElement[];
};

const MoreToolsButton = forwardRef<HTMLButtonElement, MoreToolsButtonProps>(
  function MoreToolsButton(props, ref) {
    const { children, ...restProps } = props;

    return (
      <Popover>
        <PopoverTrigger asChild>
          <RichEditorToolbarButton
            ref={ref}
            label="More formatting tools"
            icon={ThreeDots}
            {...restProps}
          />
        </PopoverTrigger>

        <PopoverContent side="top" align="start">
          <Flex>{children}</Flex>
        </PopoverContent>
      </Popover>
    );
  }
);
