import React, { ReactNode } from "react";
import { useListBox, useListBoxSection, useOption } from "react-aria";

import { st } from "@rollouthq/ui/theme";
import { dataAttr } from "@rollouthq/ui/utils/dom";

import type { AriaListBoxOptions } from "@react-aria/listbox";
import type { Node } from "@react-types/shared";
import type { ListState } from "react-stately";

interface ListBoxProps extends AriaListBoxOptions<unknown> {
  listBoxRef?: React.RefObject<HTMLUListElement>;
  state: ListState<unknown>;
  itemWithButton?: boolean;
  contentHeader?: ReactNode;
}

interface SectionProps {
  section: Node<unknown>;
  itemWithButton?: boolean;
  state: ListState<unknown>;
}

interface OptionProps {
  item: Node<unknown>;
  itemWithButton?: boolean;
  state: ListState<unknown>;
}

export function ListBox(props: ListBoxProps) {
  const ref = React.useRef<HTMLUListElement>(null);
  const { listBoxRef = ref, state, itemWithButton } = props;
  const { listBoxProps } = useListBox(
    { ...props, autoFocus: "first" },
    state,
    listBoxRef
  );

  React.useEffect(() => {
    let focusedKey = state.selectionManager.focusedKey;
    if (!state.collection.getItem(focusedKey)) {
      let firstKey = state.collection.getFirstKey();
      if (firstKey) {
        state.selectionManager.setFocusedKey(firstKey);
      }
    }
  });

  return (
    <ul
      className="rollout-listbox rollout-combobox__content"
      {...listBoxProps}
      ref={listBoxRef}
    >
      {Array.from(state.collection).map((item) =>
        item.type === "section" ? (
          <ListBoxSection key={item.key} section={item} state={state} />
        ) : (
          <Option
            key={item.key}
            item={item}
            state={state}
            itemWithButton={itemWithButton}
          />
        )
      )}
    </ul>
  );
}

function ListBoxSection({ section, state, itemWithButton }: SectionProps) {
  const { itemProps, headingProps, groupProps } = useListBoxSection({
    heading: section.rendered,
    "aria-label": section["aria-label"] || "ariaLabel",
  });

  const hasItems = !!Array.from(section.childNodes).length;

  if (!hasItems) {
    return null;
  }

  return (
    <>
      <st.li
        css={{ paddingTop: "$2" }}
        {...itemProps}
        className="rollout-listbox__section"
      >
        {section.rendered && (
          <st.span
            {...headingProps}
            css={{
              fontSize: "$1",
              fontWeight: "$bold",
              textTransform: "uppercase",
              marginX: "$3",
              color: "black",
            }}
          >
            {section.rendered}
          </st.span>
        )}

        <ul {...groupProps}>
          {Array.from(section.childNodes).map((node) => (
            <Option
              key={node.key}
              item={node}
              state={state}
              itemWithButton={itemWithButton}
            />
          ))}
        </ul>
      </st.li>
    </>
  );
}

function Option({ item, state, itemWithButton }: OptionProps) {
  const ref = React.useRef<HTMLLIElement>(null);
  const { optionProps, isDisabled, isSelected, isFocused } = useOption(
    {
      key: item.key,
    },
    state,
    ref
  );

  return (
    <li
      {...optionProps}
      ref={ref}
      className="rollout-listbox__option"
      style={itemWithButton ? { padding: "0px", width: "100%" } : {}}
      data-focused={dataAttr(isFocused)}
      data-selected={dataAttr(isSelected)}
      data-disabled={dataAttr(isDisabled)}
    >
      {item.rendered}
    </li>
  );
}
