import {
  CalendarDate,
  getDayOfWeek,
  isSameDay,
  isSameMonth,
  isToday,
} from "@internationalized/date";
import { AriaCalendarCellProps, useCalendarCell } from "@react-aria/calendar";
import { useFocusRing } from "@react-aria/focus";
import { useLocale } from "@react-aria/i18n";
import { useHover } from "@react-aria/interactions";
import { mergeProps } from "@react-aria/utils";
import { CalendarState, RangeCalendarState } from "@react-stately/calendar";
import classNames from "classnames";
import React, { useRef } from "react";

import { Button } from "@rollouthq/ui/components/button";
import { Flex } from "@rollouthq/ui/theme";

interface CalendarCellProps extends AriaCalendarCellProps {
  state: CalendarState | RangeCalendarState;
  currentMonth: CalendarDate;
}

export function CalendarCell({
  state,
  currentMonth,
  ...props
}: CalendarCellProps) {
  let ref = useRef<HTMLButtonElement | null>(null);
  let {
    cellProps,
    buttonProps,
    isPressed,
    isSelected,
    isDisabled,
    isFocused,
    formattedDate,
  } = useCalendarCell(
    {
      ...props,
      isDisabled: !isSameMonth(props.date, currentMonth),
    },
    state,
    ref
  );
  let isUnavailable = state.isCellUnavailable(props.date) && !isDisabled;
  let isLastSelectedBeforeDisabled =
    !isDisabled && state.isCellUnavailable(props.date.add({ days: 1 }));
  let isFirstSelectedAfterDisabled =
    !isDisabled && state.isCellUnavailable(props.date.subtract({ days: 1 }));
  let highlightedRange = "highlightedRange" in state && state.highlightedRange;
  let isSelectionStart =
    isSelected &&
    highlightedRange &&
    isSameDay(props.date, highlightedRange.start);
  let isSelectionEnd =
    isSelected &&
    highlightedRange &&
    isSameDay(props.date, highlightedRange.end);
  let { locale } = useLocale();
  let dayOfWeek = getDayOfWeek(props.date, locale);
  let isRangeStart =
    isSelected &&
    (isFirstSelectedAfterDisabled || dayOfWeek === 0 || props.date.day === 1);
  let isRangeEnd =
    isSelected &&
    (isLastSelectedBeforeDisabled ||
      dayOfWeek === 6 ||
      props.date.day === currentMonth.calendar.getDaysInMonth(currentMonth));
  let { focusProps, isFocusVisible } = useFocusRing();
  let { hoverProps, isHovered } = useHover({
    isDisabled: isDisabled || isUnavailable || state.isReadOnly,
  });

  return (
    <Flex
      {...cellProps}
      css={{
        justifyContent: "center",
        alignItems: "center",
        color: "black",
        ".calendar-cell": {
          size: "10px",
          borderWidth: "1px",
          borderStyle: "solid",
          borderColor: "transparent",
        },
        ".is-today": {
          borderColor: "$primary",
        },
        ".is-selected": {
          bg: "$primary",
          color: "white",
        },
        ".is-selected:hover": {
          bg: "$primaryDark",
        },
        ".is-focused": {
          borderColor: "$grayMedium",
        },
      }}
    >
      <Button
        variant="ghost"
        css={{
          borderRadius: "$full",
          // size: "10px",
          justifyContent: "center",
          alignItems: "center",
        }}
        disabled={isDisabled}
        {...mergeProps(buttonProps, hoverProps, focusProps)}
        ref={ref}
        className={classNames({
          "calendar-cell": true,
          "is-today": isToday(props.date, state.timeZone),
          "is-selected": isSelected,
          "is-focused": isFocused && isFocusVisible,
          "is-hovered": isHovered,
          // "is-range-start": isRangeStart,
          // "is-range-end": isRangeEnd,
          // "is-range-selection": isSelected && "highlightedRange" in state,
          // "is-selection-start": isSelectionStart,
          // "is-selection-end": isSelectionEnd,
          // "is-pressed": isPressed && !state.isReadOnly,
          // "is-outsideMonth": !isSameMonth(props.date, currentMonth),
          // "is-unavailable": isUnavailable,
        })}
      >
        <span>{formattedDate}</span>
      </Button>
    </Flex>
  );
}
