import React from "react";
import { ReactElement } from "react";

import { dataAttr } from "@rollouthq/ui/utils/dom";

export type FormControlProps = {
  label?: string;
  description?: string;
  error?: string;
  isRequired?: boolean;
  children: ReactElement;
};

export function FormControl(props: FormControlProps) {
  const { children, label, description, error, isRequired = false } = props;

  return (
    <div className="rollout-form-control">
      {label && (
        <div className="rollout-form-control__label">
          {label}
          {isRequired && (
            <span className="rollout-form-control__required-hint">
              {" "}
              (required)
            </span>
          )}
        </div>
      )}

      {description && (
        <div className="rollout-form-control__description">{description}</div>
      )}

      <div
        className="rollout-form-control__input"
        data-error={dataAttr(!!error)}
      >
        {children}
      </div>

      {error && <div className="rollout-form-control__error">{error}</div>}
    </div>
  );
}
